import {forger} from '../RequestForgerer'
import URLS from './urls';


class AirDrops {
    getLaunchPads(address, {page =1, pageSize= 10}) {
        // prepare url
        let url = address ? URLS.GET_ALL_LAUNCHPADS+"?address="+address +"&page=" + page + "&pageSize=" + pageSize : URLS.GET_ALL_LAUNCHPADS +"?page=" + page + "&pageSize=" + pageSize;
        console.log(url);
        // forge the url
        return forger.forge({
            method:'GET',
            url:url,
        });
    };


    getSingleLaunchPad(id){
        // prepare url
        let url = URLS.GET_SINGLE_LAUNCHPAD;
        console.log(url);
        // forge the url
        return forger.forge({
            method:'GET',
            url:url+id,
        });
    }

    createLaunchpad(payload){
        // prepare url
        let url = URLS.GET_ALL_LAUNCHPADS;
        // forge the url
        return forger.forge({
            method:'POST',
            url: URLS.BACKEND_URL + "/process",
            payload: {
                url,
                params: payload,
            }
        });
    }

    updateALaunchpad(payload){
        // prepare url
        let url = URLS.GET_ALL_LAUNCHPADS;
        console.log(url);
        // forge the url
        return forger.forge({
            method:'PUT',
            url:url,
            payload:payload
        });
    }

    getLaunchpadInvestmentHistory(address, id){
        // prepare url
        let url = URLS.GET_LAUNCHPAD_HISTORY;
        console.log(url);
        // forge the url
        return forger.forge({
            method:'POST',
            url:url,
            payload:{
                launchpadId:id,
                address
            }
        });
    }


    getLaunchpadInfo(launchpadId){
        // prepare url
        let url = URLS.GET_LAUNCHPAD_INFO;
        console.log(url);
        // forge the url
        return forger.forge({
            method:'POST',
            url:url,
            payload:{
                launchpadId,
            }
        });
    }


    closePresale(launchpadId){
        // prepare url
        let url = URLS.CLOSE_PRESALE;
        console.log(url);
        // forge the url
        return forger.forge({
            method:'POST',
            url:url,
            payload:{
                launchpadId,
            }
        });
    }

    closeIlo(launchpadId){
        // prepare url
        let url = URLS.CLOSE_ILO;
        console.log(url);
        // forge the url
        return forger.forge({
            method:'POST',
            url:url,
            payload:{
                launchpadId,
            }
        });
    }


    getLaunchpadSubmissions(launchpadId, {limit, offset}){
        let url = URLS.GET_LAUNCHPAD_SUBMISSION;
        console.log(url);
        // forge the url
        return forger.forge({
            method:'GET',
            url:url,
            payload:{
                launchpadId,
                limit, offset
            }
        });
    }
}


export default AirDrops;
