import {forger} from '../RequestForgerer'
import URLS from './urls';


class Tokens {
    getAllTokens(keyword, {page, pageSize} = {}) {
        console.log("keyword", keyword);
        // prepare url
        let url = keyword ? URLS.GET_ALL_TOKENS + "?keyword=" + keyword : URLS.GET_ALL_TOKENS + "?page=" + page + "&pageSize=" + pageSize ;
        
        if (!keyword && !page && !pageSize) url = URLS.GET_ALL_TOKENS;
        
        console.log(url);
        // forge the url
        return forger.forge({
            method: 'GET',
            url
        });
    };

    createAnewToken(payload){
        // prepare url
        let url = URLS.GET_ALL_TOKENS;
        console.log(url);
        // forge the url
        return forger.forge({
            method:'POST',
            url:url,
            payload
        });
    }

    updateANewToken(payload){
        // prepare url
        let url = URLS.GET_ALL_TOKENS;
        console.log(url);
        // forge the url
        return forger.forge({
            method:'PUT',
            url:url,
            payload
        });
    }


}


export default Tokens;
