import React, { Component } from "react";
import { ModalHeader, ModalBody, ModalFooter, Button, Modal } from "reactstrap";
import PropTypes from "prop-types";
import "../../assets/css/globalModal.css";

class GlobalModal extends Component {
  constructor(props) {
    super(props);

    this.state = {
      loading: false,
      ref: "modal_ref_" + Math.ceil(Math.random() * 100),
    };
  }

  componentDidMount() {
    this.mounted = true;
  }

  componentWillUnmount() {
    this.mounted = false;
  }

  withdrawAirdropTokens() {}

  getConfiguredButton(buttonElem, modalConfig) {
    let onClickFunction = () => {
      let mainfunction =
        buttonElem.onClick === "onCancel"
          ? modalConfig.onCancel
          : buttonElem.onClick === "onConfirm"
          ? modalConfig.onConfirm
          : buttonElem.onClick;

      if (buttonElem.loaderOnClick === true) {
        return async () => {
          this.setState({
            loading: true,
          });
          let data = this.refs[this.state.ref]?.state;
          if (!data) data = {};
          data["ModalActionsExtra"] = {
            closeModal: () => this.props.closeModal(modalConfig.id),
            modalConfig,
            refs: this.refs[this.state.ref],
          };
          await mainfunction(data);

          if (this.mounted) {
            this.setState({
              loading: false,
            });
          }
        };
      } else {
        let data = this.refs[this.state.ref]?.state;
        if (data) {
          data["ModalActionsExtra"] = {
            closeModal: () => this.props.closeModal(modalConfig.id),
            modalConfig,
            refs: this.refs[this.state.ref],
          };
        }
        return () => mainfunction(data);
      }
    };

    let onClickEvent = onClickFunction();
    return (
      <Button
        key={buttonElem.title}
        className={buttonElem.class}
        color={buttonElem.color ?? "primary"}
        onClick={onClickEvent}
      >
        {buttonElem.title}
      </Button>
    );
  }

  render() {
    const { modalConfig, closeModal } = this.props;
    let { loading, ref } = this.state;
    const { body: EditDelivery } = modalConfig;
    let TMP = EditDelivery.type;
    let modalSpecificProps = {
      modalId: modalConfig.id,
      closeModal: () => closeModal(modalConfig.id),
    };
    return (
      <Modal
        isOpen={!!modalConfig}
        toggle={loading !== true ? modalConfig.onCancel : () => {}}
        className={"modal-dialog-centered"}
        fade={modalConfig.fade ?? false}
        style={modalConfig.globalModalStyle}
        contentClassName={""}
      >
        <ModalHeader
          className={""}
          toggle={loading !== true ? modalConfig.onCancel : () => {}}
        >
          {modalConfig.header}
        </ModalHeader>
        <ModalBody
          className={"globalModal" + (loading === true ? "loadingx" : "")}
        >
          <TMP
            {...EditDelivery.props}
            {...(typeof TMP.type === "function" ? modalSpecificProps : {})}
            ref={ref}
          />
        </ModalBody>
        <ModalFooter className={""}>
          {modalConfig.buttons
            ? modalConfig.buttons.map((elem) => {
                return this.getConfiguredButton(elem, modalConfig);
              })
            : ""}
        </ModalFooter>
      </Modal>
    );
  }
}

export default GlobalModal;

GlobalModal.propTypes = {
  modalConfig: PropTypes.object,
};
