import {forger} from '../RequestForgerer'
import URLS from './urls';

class Utils {
    getGlobalState() {
        let url = URLS.GET_GLOBAL_STATE;
        return forger.forge({
            method:'GET',
            url
        });
    }

    setGlobalState(payload) {
        let url = URLS.GLOBAL_STATE;
        return forger.forge({
            method:'POST',
            url,
            payload
        });
    }

    getAllVariables() {
        // prepare url
        let url = URLS.GET_ALL_VARS
        console.log(url);
        // forge the url
        return forger.forge({
            method:'GET',
            url:url,
        });
    };

    updateVariables(payload) {
        // prepare url
        let url = URLS.GET_ALL_VARS
        console.log(url);
        // forge the url
        return forger.forge({
            method:'PUT',
            url:url,
            payload
        });
    };

    updateBlockchainVariables(payload) {
        // prepare url
        let url = URLS.UPDATE_BLOCKCHAIN_VARS
        console.log(url);
        // forge the url
        return forger.forge({
            method:'PUT',
            url:url,
            payload
        });
    };


    getImage(url){
        return forger.forge({
            method:'GET',
            url:url,
            config: {
                responseType: 'arraybuffer'
            }
        }).then((response)=> Buffer.from(response.data, 'binary').toString('base64'));
    }
    

}


export default Utils;
