import React from 'react';
import {Button, Col, Form, Row} from "react-bootstrap";
import Switch from "react-switch";
import {Divider} from "@material-ui/core";
import AsyncSelect from 'react-select/async'
import {Launchpads, Tokens} from '../../services/Requests'
import { FaDoorClosed } from 'react-icons/fa';
import {verificationModal} from "../Utils/utilModals";

class CreateANewLaunchpad extends React.Component{
    constructor(props) {
        super(props);
        this.state={
            links: ['Twitter','Facebook', 'Telegram', 'Medium', 'Website', 'whitePaper', 'Video'],
            tokens:[],
            form:{
                ...(props.form ?? {})
            },
            bcData:null,
            formRef : "formRefLaunchpad",
        }
    }

    getTokens = (inputValue) => {
        return new Promise((resolve) => {
            let TK = new Tokens();
            TK.getAllTokens(inputValue).then((response)=>{
                resolve(response.data.data.map((token)=>{
                    return {
                        value: token.id,
                        label: token.name
                    }
                }))
            })
        })
    };

    isThisAnUpdate(){
        return !!this.state.form?.id;
    }

    setFormValue(name, value){
        this.setState({
            form:{
                ...this.state.form,
                [name]: value
            }
        })
    }

    closePresale(tokenId){
        if(!this.isThisAnUpdate()) return ;
        let LA = new Launchpads();
        return LA.closePresale(tokenId);
    }

    closeIlo(tokenId){
        if(!this.isThisAnUpdate()) return ;
        let LA = new Launchpads();
        return LA.closeIlo(tokenId);
    }


    saveNewLaunchpad = (event) => {
        console.log(this.refs[this.state.formRef].elements);
        let payload = Object.keys(this.refs[this.state.formRef].elements).filter((e)=>isNaN(parseInt(e))).map((e)=>{
            return {[e]:this.refs[this.state.formRef].elements[e].type === "number" ? parseFloat(this.refs[this.state.formRef].elements[e].value) : this.refs[this.state.formRef].elements[e].value}
        }).reduce((prev, curr)=>{return{...prev, ...curr}},{})
        payload["token_id"] = this.state.form.token_id;
        payload["status"] = this.state.form.status;
        payload["hidden"] = this.state.form?.hidden;
        payload["with_ilo"] = this.state.form?.with_ilo;
        payload["with_presale"] = this.state.form?.with_presale;
        if(!payload["with_presale"]){
            payload['presale_cap'] = 0;
            payload['presale_token_value'] = 0;
        }
        if(!payload["with_ilo"]){
            payload['ilo_cap'] = 0;
            payload['ilo_token_value'] = 0;
        }
        Object.keys(payload).filter(e => e.includes('react-select-')).forEach((e)=>{
            delete payload[e];
        })
        if(this.state.form?.id){
            payload['id'] = this.state.form.id;
        }
        this.setState({
            form: payload
        })
        return payload;

    }

    componentDidMount() {
        console.log("componentDidMount");
        if(this.state.form.id){
            let LA = new Launchpads();
            LA.getLaunchpadInfo(this.state.form.id).then((response)=>{
                this.setState({
                    bcData:response.data
                })
            })
        }
    }

    render(){
        const {links, form, bcData} = this.state;
        this.formRef = this.refs[this.state.formRef];
        return <Form ref={this.state.formRef} className={"w-100"} onSubmit={(ev) => this.saveNewAirdrop(ev)}>
            <Row>
                <Col>
                    <Form.Group controlId="name">
                        <Form.Label>Name*</Form.Label>
                        <Form.Control defaultValue={form?.name} type="text" placeholder="Enter email" />
                        <Form.Text className="text-muted">
                            Input the airdrop Name
                        </Form.Text>
                    </Form.Group>

                    <Form.Group controlId="description">
                        <Form.Label>Description</Form.Label>
                        <Form.Control  defaultValue={form?.description} as="textarea" type="text" placeholder="description" />
                    </Form.Group>
                    <Form.Group controlId="cap">
                        <Form.Label>Token Cap</Form.Label>
                        <Form.Control  defaultValue={form?.cap} type="Number" placeholder="Token Cap" />
                    </Form.Group>
                    <Form.Group controlId="cap_value">
                        <Form.Label>Token Cap Value ($)</Form.Label>
                        <Form.Control  defaultValue={form?.cap_value} type="Number" placeholder="Token Cap value" />
                    </Form.Group>
                    <Form.Group controlId="token_value">
                        <Form.Label>Token Value ($)</Form.Label>
                        <Form.Control  defaultValue={form?.token_value} type="Number" placeholder="Token value outside the presale and ILO" />
                    </Form.Group>
                    <Divider className={"my-2"}/>
                    <div className={"d-flex"}>
                        <h4 className={"my-auto"}>{this.state.form?.with_presale === 1 ? "With Presale": "Without Presale"}</h4>  <Switch className={"ml-auto"} onColor="#56D663" checked={this.state.form?.with_presale===1}  onChange={(event)=>this.setFormValue('with_presale', event?1:0)}/>
                    </div>
                    <Form.Group controlId="presale_cap">
                        <Form.Label>Presale Cap</Form.Label>
                        <Form.Control disabled={this.state.form?.with_presale !== 1}  defaultValue={form?.presale_cap} type="Number" placeholder="Maximum tokens to be sold during the presale" />
                        {
                            bcData && <Form.Text className="text-warning">
                                Current Cap : {bcData._presaleAmount * Math.pow(10, -form?.token?.decimals)}, setting this amount will reset the count
                            </Form.Text>
                        }
                        {
                            bcData && <Form.Text className="text-warning">
                                Current left : {bcData.remainPresale * Math.pow(10, -form?.token?.decimals)}, setting this amount will reset the count
                            </Form.Text>
                        }
                    </Form.Group>
                    <Form.Group controlId="presale_token_value">
                        <Form.Label>Token Value ($)</Form.Label>
                        <Form.Control disabled={this.state.form?.with_presale !== 1} defaultValue={form?.presale_token_value} type="Number" placeholder="value of the token during the presale" />
                    </Form.Group>
                    <Divider className={"my-2"} />
                    <div className={"d-flex"}>
                        <h4 className={"my-auto"}>{this.state.form?.with_ilo === 1 ? "With ILO": "Without ILO"}</h4>  <Switch className={"ml-auto"} onColor="#56D663" checked={this.state.form?.with_ilo===1}  onChange={(event)=>this.setFormValue('with_ilo', event?1:0)}/>
                    </div>
                    <Form.Group controlId="ilo_cap">
                        <Form.Label>ILO Cap</Form.Label>
                        <Form.Control disabled={this.state.form?.with_ilo !== 1} defaultValue={form?.ilo_cap} type="Number" placeholder="Maximum tokens to be sold during the ILO" />
                        {
                            bcData && <Form.Text className="text-warning">
                                Current Cap : {bcData._ILOAmount * Math.pow(10, -form?.token?.decimals)},
                            </Form.Text>
                        }
                        {
                            bcData && <Form.Text className="text-warning">
                                Current Amount left : {bcData.remainILO * Math.pow(10, -form?.token?.decimals)},
                            </Form.Text>
                        }
                    </Form.Group>
                    <Form.Group controlId="ilo_token_value">
                        <Form.Label>Token Value ($)</Form.Label>
                        <Form.Control disabled={this.state.form?.with_ilo !== 1} defaultValue={form?.ilo_token_value} type="Number" placeholder="value of the token during the ILO" />
                    </Form.Group>
                </Col>
                <Col>
                    <h4 className={"mt-2"} >Token {form ? <small>{form?.token?.name} ({form?.token?.id})</small>: ''}</h4>
                    <AsyncSelect
                        controlId="token_id"
                        isSearchable={true}
                        loadOptions={this.getTokens}
                        defaultValue={this.state.form?.token_id}
                        defaultOptions
                        onChange={(event)=>{
                            this.setState({
                                form:{
                                    ...this.state.form,
                                    token_id: event.value
                                }
                            })
                        }}
                    >
                    </AsyncSelect>
                    {
                        form?.id && bcData && <Form.Group controlId="hidden" >
                            <Form.Label>Balance of presale in {form?.token?.name}</Form.Label>
                            <div className={"d-flex"}>
                                <span>around : {Math.floor(parseInt(bcData?.balanceOfPresale) / form?.token?.decimals)}</span>
                            </div>
                        </Form.Group>
                    }
                    <Form.Group>
                        <Form.Label>Status</Form.Label>
                        <select
                            onChange={((event) => {
                                this.setState({form:{...this.state.form, status: parseInt(event.target.value)}})
                            })}
                            value={this.state.form?.status}
                            className="form-control form-control-sm font-weight-bold mr-4 border-0 bg-light">
                            <option value="1">ILO</option>
                            <option value="0">Presale</option>
                            <option value="2">Completed</option>
                        </select>
                    </Form.Group>

                    <Divider className={"my-2"} />

                    <h4 className={"mt-2"}>Trees</h4>
                    <Form.Group controlId="trees_to_be_planted">
                        <Form.Label>N. of trees to be planted</Form.Label>
                        <Form.Control defaultValue={form?.trees_to_be_planted} type="Number" placeholder="Number of trees to be planted on each swap (changes only affect new swaps)" />
                    </Form.Group>


                    <Divider className={"my-2"} />

                    <Form.Group controlId="hidden">
                        <Form.Label>Hide Launchpad</Form.Label>
                        <div className={"d-flex"}>
                                <span className={"my-auto"}>{this.state.form?.hidden ? "Launchpad hidden": "Launchpad visible"}</span>  <Switch className={"ml-auto"} onColor="#56D663" checked={this.state.form?.hidden===1}  onChange={(event)=>this.setFormValue('hidden', event?1:0)}/>
                        </div>
                    </Form.Group>

                    <h4 className={"mt-2"}>Social</h4>
                    {
                        links.map(e=>{
                            return <Form.Group key={e.toLowerCase()} controlId={e.toLowerCase()}>
                                <Form.Label>{e} link</Form.Label>
                                <Form.Control defaultValue={form[e]} placeholder={`${e.toLowerCase()} link`} />
                            </Form.Group>
                        })
                    }
                </Col>
                {
                    this.isThisAnUpdate() && <Col>
                        <Button disabled={this.state?.form?.with_presale !== 1} className={"w-100 my-2"} variant={"danger"} onClick={()=>verificationModal(() => this.closePresale(form?.id), {body:"You are about to close the Presale of this launchpad"})}><FaDoorClosed/>  Close Presale </Button>
                        <Button disabled={this.state?.form?.with_ilo !== 1} className={"w-100 my-2"} variant={"danger"} onClick={()=>verificationModal(() => this.closeIlo(form?.id), {body:"You are about to close the ILO of this launchpad"})}><FaDoorClosed/>  Close ILO</Button>
                    </Col>
                }
            </Row>
        </Form>
    }
}

export default CreateANewLaunchpad;
