import React from 'react';
import {Col, Form, Row} from "react-bootstrap";
import {Tokens} from '../../services/Requests'
import {toPublicBackendUrl} from "../../../_metronic/_helpers";

class CreateANewToken extends React.Component{

    constructor(props) {
        super(props);
        this.state={
            form:{
                ...(props.form ?? {}),
                icon:undefined
            },
            showIcon: props.form?.icon ? toPublicBackendUrl(props.form?.icon) : null,
            urlIcon: false,
            formRef : "formRefToken",
            linkImage: null
        }
    }

    getTokens(inputValue){
        let TK = new Tokens();
        return TK.getAllTokens(inputValue).then((response) => response.data.map((e)=>{
            return {value: e.id, label: e.name}
        }))
    }

    saveANewToken = (event) => {
        let payload = Object.keys(this.refs[this.state.formRef].elements).filter((e)=>isNaN(parseInt(e))).map((e)=>{
            return {[e]:this.refs[this.state.formRef].elements[e].value}
        }).reduce((prev, curr)=>{return{...prev, ...curr}},{})
        payload["icon"] = this.state.form.icon;
        Object.keys(payload).filter(e => e.includes('react-select-')).forEach((e)=>{
            delete payload[e];
        })
        console.log(payload);
        if(this.state.form?.id){
            payload['id'] = this.state.form.id;
        }
        this.setState({
            form: payload
        })
        return payload;
    }

    isValidHttpUrl(string) {
        let url;

        try {
            url = new URL(string);
        } catch (_) {
            return false;
        }
        console.log(url);

        return (url.protocol === "http:" || url.protocol === "https:") && string.match(/\.(jpeg|jpg|gif|png)$/) != null;
    }

    getUrlImage(url){
        if(this.isValidHttpUrl(url)){
            this.setState({
                form:{
                    ...this.state.form,
                    icon: url
                },
                showIcon: url,
                urlIcon: true
            })
        }
    }

    render(){
        const {form} = this.state;
        this.formRef = this.refs[this.state.formRef];
        return <Form ref={this.state.formRef} className={"w-100"} onSubmit={(ev) => this.saveNewAirdrop(ev)}>
            <Row>
                <Col>
                    <Form.Group controlId="name">
                        <Form.Label>Name*</Form.Label>
                        <Form.Control defaultValue={form?.name} type="text" placeholder="token name" />
                        <Form.Text className="text-muted">
                            Input the airdrop Name
                        </Form.Text>
                    </Form.Group>

                    <Form.Group controlId="ticker">
                        <Form.Label>Ticker</Form.Label>
                        <Form.Control  defaultValue={form?.ticker} as="textarea" type="text" placeholder="token ticker" />
                    </Form.Group>
                    <Form.Group controlId="address">
                        <Form.Label>Token Address</Form.Label>
                        <Form.Control  defaultValue={form?.address} type="text" placeholder="Token address" />
                    </Form.Group>
                    <Form.Group controlId="decimals">
                        <Form.Label>Token Decimals</Form.Label>
                        <Form.Control  defaultValue={form?.decimals ?? 18} type="number" placeholder="Token decimals" />
                    </Form.Group>
                </Col>
                <Col>
                    <Form.Group controlId="icon" className="mb-3">
                        <Form.Label>Token icon</Form.Label>
                        <Form.Control className={"mb-3"} onChange={(event)=>this.getUrlImage(event.target.value)} type="text" placeholder="Token icon url" />
                        <Form.Control onChange={(event)=>{
                            let file = event.target.files[0];
                            if(file){
                                const reader = new FileReader();
                                reader.onload = (readerEvent) =>{
                                    let binaryString = readerEvent.target.result;
                                    this.setState({
                                        form:{
                                            ...this.state.form,
                                            icon: 'data:image/png;base64,' + btoa(binaryString)
                                        },
                                        showIcon: 'data:image/png;base64,' + btoa(binaryString),
                                        urlIcon: false
                                    })
                                }
                                reader.readAsBinaryString(file);
                            }
                        }} type="file" accept={".png"} />
                    </Form.Group>
                    <div className={"my-2"}>
                        <img alt="Not found" className={"w-100"} src={this.state.showIcon}/>
                    </div>
                </Col>
            </Row>
        </Form>
    }
}

export default CreateANewToken;
