import React from "react";
import * as moment from "moment";
import {Airdrops} from "../../services/Requests";
import Table from "../common/table";
import {getVisibleModal} from "../../../redux/reducer/alertsReducer";
import {connect} from "react-redux";
import {closeAlert, showAlert} from "../../../redux/actions/alertActions";
import ShortFormAddress from "../common/shortFormAddress";

class SingleAirdropSubmissions extends React.Component{
    constructor(props) {
        super(props);
        this.state={
            airdropSubmissionsTableConfig: {},
            startingPage: 1,
            rowCount: null,
            initialPageSize: 10,
            tableHeaders: [
                {
                    id:"wallet",
                    classNames: "",
                    style:{
                        width:80
                    },
                    value : "Wallet",
                    cell: {
                        render: (value) =>{
                            return <ShortFormAddress address={value?.address}/>
                        }
                    }
                },
                {
                    id:"isWinning",
                    classNames: "",
                    style:{
                        width:50
                    },
                    value: "Has Won",
                    cell:{
                        render: (value)=>{
                            return parseInt(value, 10) === 1 ? 'Yes': 'No'
                        }
                    }
                },
                {
                    id:"withdrawn",
                    classNames: "",
                    style:{
                        width:50
                    },
                    value: "Has Withdrawn",
                    cell:{
                        render: (value)=>{
                            return parseInt(value, 10) === 1 ? 'Yes': 'No'
                        }
                    }
                },
                {
                    id:"createdAt",
                    classNames: "",
                    style:{
                        width:50
                    },
                    value: "Submission Date",
                    cell:{
                        render: (value)=>{
                            return moment(value).format('mm/DD/yyyy HH:mm')
                        }
                    }
                },
                {
                    id:"actions",
                    classNames: "",
                    style:{
                        width:50
                    },
                    value: "Actions",
                    cell:{
                        render: (value, row)=>{
                            return ''
                        }
                    }
                },
            ],
            data: [],
        }
    }


    componentDidMount() {
        this.refreshAirdropSubmissions();
    }

    refreshAirdropSubmissions(){
        return this.getSingleAirdropSubmissions({limit:this.state.initialPageSize, offset:(this.state.startingPage - 1) * this.state.initialPageSize})
    }


    getSingleAirdropSubmissions({limit, offset}){
        let AR = new Airdrops();
        return AR.getAirdropSubmissions(this.props.airdropId, {limit, offset}).then((response)=>{
            this.setState({
                data: response.data.data,
                rowCount: response.data.rowCount
            });
            return Promise.resolve(true);
        });
    }

    onPaginationChanges(pagination){
        this.getSingleAirdropSubmissions({
            limit: pagination.pageSize,
            offset: pagination.pageSize * (pagination.page - 1)
        })
    }


    render() {
        const {tableHeaders, data, rowCount} = this.state;

        return (
            <div>
                <Table
                    tableConfig={
                        {
                            headers: tableHeaders,
                            initialPageSize: 10,
                            startingPage: 1
                        }
                    }
                    rowCount={rowCount}
                    onPaginationChanges={(data)=>this.onPaginationChanges(data)}
                    data={data}
                />
            </div>
        );
    }
}


const mapRouterStateToProps = state => ({
    globalModal: state.globalModal,
    visibleModal: getVisibleModal(state.alerts),
});



export default connect(
    mapRouterStateToProps,
    {closeModal: closeAlert, showModal: showAlert}
)(SingleAirdropSubmissions);
