import {forger} from "../../../services/Requests/RequestForgerer";
import RequestURLS from "../../../services/Requests/Requests/urls";

const BACKEND_URL = RequestURLS.BACKEND_URL;

export const LOGIN_URL = `${BACKEND_URL}/users/signIn/`;
export const REGISTER_URL = `${BACKEND_URL}/users/signUp`;
export const REQUEST_PASSWORD_URL = "users/forgotPassword";
export const ME_URL = `${BACKEND_URL}/users/getUser`;

export function login(email, password) {
  return forger.forge({method: 'POST', url: LOGIN_URL, payload:{ email, password }});
}

export function register(email, name, password) {
  return forger.forge({method:'POST', url:REGISTER_URL, payload:{ email, name, password }});
}

export function requestPassword(email) {
  return forger.forge({method:'POST', url:REQUEST_PASSWORD_URL, payload:{ email }});
}

export function getUserByToken() {
  // Authorization head should be fulfilled in interceptor.
  return forger.forge({method:'POST', url:ME_URL, payload:{}});
}
