import React from "react";
import '../../assets/css/componenets/shortFormAddress.css';
import PropTypes from 'prop-types';

class ShortFormAddress extends React.Component{
    getAddressShortForm(address){
        let value = address.match(/^([\s\S]{8})([\s\S]*?)([\s\S]{8})$/i);
        return value[1]+' .... '+value[3];
    }

    getBscscanLink = (address) => {
        return `https://bscscan.com/address/${address}`
    }

    render() {
        return (
            <div className={this.props.className + " d-flex flex-grow"}>
                <a 
                    className={"addressLink"} 
                    target="_blank" 
                    rel="noopener noreferrer"
                    href={this.props.href ?? this.getBscscanLink(this.props.address)}
                >
                    {this.getAddressShortForm(this.props.address)}
                </a>
            </div>
        );
    }
}

ShortFormAddress.propTypes = {
    onSignOutClick: PropTypes.func,
    className: PropTypes.string,
    href: PropTypes.string,
    address: PropTypes.string,
};


export default ShortFormAddress;
