/* eslint-disable jsx-a11y/anchor-is-valid */
import React from 'react';
import * as moment from 'moment';
import { Wallets } from '../../services/Requests';
import Table from '../../modules/common/table';
import { getVisibleModal } from '../../../redux/reducer/alertsReducer';
import { connect } from 'react-redux';
import { closeAlert, showAlert } from '../../../redux/actions/alertActions';
import ShortFormAddress from '../../modules/common/shortFormAddress';
import FacebookIcon from '../../assets/icons/facebook-3.svg';
import TwitterIcon from '../../assets/icons/twitter.svg';
import TelegramIcon from '../../assets/icons/telegram.svg';
import { verificationModal } from '../../modules/Utils/utilModals';

class WalletsPage extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      loading: false,
      airdropSubmissionsTableConfig: {},
      startingPage: 1,
      rowCount: null,
      initialPageSize: 10,
      tableHeaders: [
        {
          id: 'address',
          classNames: '',
          style: {
            width: 150,
          },
          value: 'Wallet',
          cell: {
            render: (value) => {
              return <ShortFormAddress address={value} />;
            },
          },
        },
        {
          id: 'name',
          classNames: '',
          style: {
            width: 50,
          },
          value: 'Name',
        },
        {
          id: 'description',
          classNames: '',
          style: {
            width: 50,
          },
          value: 'Bio',
        },
        {
          id: 'socialLinks',
          classNames: '',
          style: {
            width: 100,
          },
          value: 'Social',
          cell: {
            render: (value, row) => {
              return (
                <ul>
                  {row?.facebook && (
                    <li className={'d-flex'}>
                      <img alt="Facebook logo" style={{ height: 35 }} src={FacebookIcon} />
                      <a className={'my-auto ml-4'} href={row.facebook}>
                        {row.facebook}
                      </a>
                    </li>
                  )}
                  {row?.twitter && (
                    <li className={'d-flex'}>
                      <img alt="twitter" style={{ height: 35 }} src={TwitterIcon} />
                      <a className={'my-auto ml-4'} href={row.twitter}>
                        {row.twitter}
                      </a>
                    </li>
                  )}
                  {row?.telegram && (
                    <li className={'d-flex'}>
                      <img alt="telegram" style={{ height: 35 }} src={TelegramIcon} />
                      <a className={'my-auto ml-4'} href={row.telegram}>
                        {row.telegram}
                      </a>
                    </li>
                  )}
                </ul>
              );
            },
          },
        },
        {
          id: 'wallets_airdrops',
          classNames: '',
          style: {
            width: 50,
          },
          value: 'Airdrops count',
          cell: {
            render: (value) => {
              return Array.isArray(value) ? value?.length : 0;
            },
            style: {
              textAlign: 'center',
            },
          },
        },
        {
          id: 'wallets_launchpads',
          classNames: '',
          style: {
            width: 50,
          },
          value: 'Launchpads count',
          cell: {
            render: (value) => {
              return Array.isArray(value) ? value?.length : 0;
            },
            style: {
              textAlign: 'center',
            },
          },
        },
        {
          id: 'createdAt',
          classNames: '',
          style: {
            width: 50,
          },
          value: 'Profile creation date',
          cell: {
            render: (value) => {
              return moment(value).format('mm/DD/yyyy HH:mm');
            },
          },
        },
        {
          id: 'actions',
          classNames: 'text-center',
          style: {
            width: 50,
          },
          value: 'Actions',
          cell: {
            render: (value, row) => {
              return (
                <div className={'d-flex action-td'}>
                  <a
                    title='DeleteProfile'
                    className='action-finish btn btn-icon btn-light btn-hover-danger btn-sm mx-3'
                    onClick={() => {
                      verificationModal(
                        () => this.deleteProfileData(row.address),
                        { body: 'You are about to delete this profile data' },
                      );
                    }}>
                    <span className='svg-icon svg-icon-md svg-icon-info'>
                      <i className='far fa-trash-alt' style={{color: '#F45030'}} />
                    </span>
                  </a>
                </div>
              );
            },
          },
        },
      ],
      data: [],
    };
  }

  componentDidMount() {
    this.refreshWallets();
  }

  refreshWallets() {
    return this.getWallets({
      limit: this.state.initialPageSize,
      offset: (this.state.startingPage - 1) * this.state.initialPageSize,
    });
  }

  getWallets({ limit, offset }) {
    this.toggleLoading();
    let WA = new Wallets();
    return WA.getWalletsWithProfiles({ limit, offset })
      .then((response) => {
        this.setState({
          data: response.data.data,
          rowCount: response.data.rowCount,
        });
      })
      .finally(() => {
        this.toggleLoading();
        return Promise.resolve(true);
      });
  }

  toggleLoading() {
    this.setState({
      loading: !this.state.loading,
    });
  }

  onPaginationChanges(pagination) {
    this.getWallets({
      limit: pagination.pageSize,
      offset: pagination.pageSize * (pagination.page - 1),
    });
  }

  deleteProfileData(walletAddress) {
    let WA = new Wallets();
    this.toggleLoading();
    return WA.deleteProfile(walletAddress)
      .then((response) => {
        return this.refreshWallets();
      })
      .finally(() => {
        this.toggleLoading();
        return Promise.resolve(true);
      });
  }

  render() {
    const { tableHeaders, data, rowCount, loading } = this.state;

    return (
      <div>
        <div className={'p-1'}>
          <div className={`card card-custom `}>
            {/* begin::Header */}
            <div className='card-header border-0 py-5'>
              <h3 className='card-title align-items-start flex-column'>
                <span className='card-label font-weight-bolder text-dark'>
                  Wallets List
                </span>
                <span className='text-muted mt-3 font-weight-bold font-size-sm'>
                  Management page for Wallets
                </span>
              </h3>
            </div>
            <div className='card-body py-0'>
              <Table
                blocking={loading}
                tableConfig={{
                  headers: tableHeaders,
                  initialPageSize: 10,
                  startingPage: 1,
                }}
                rowCount={rowCount}
                onPaginationChanges={(data) => this.onPaginationChanges(data)}
                data={data}
              />
            </div>
          </div>
        </div>
      </div>
    );
  }
}

const mapRouterStateToProps = (state) => ({
  globalModal: state.globalModal,
  visibleModal: getVisibleModal(state.alerts),
});

export default connect(mapRouterStateToProps, {
  closeModal: closeAlert,
  showModal: showAlert,
})(WalletsPage);
