/* eslint-disable jsx-a11y/anchor-is-valid */
import React from 'react';
import { Tab, Tabs } from 'react-bootstrap';
import Airdrops from '../../services/Requests/Requests/airDrops';
import * as moment from 'moment';
import SVG from 'react-inlinesvg';
import { toAbsoluteUrl, toPublicBackendUrl } from '../../../_metronic/_helpers';
import { closeAlert, showAlert } from '../../../redux/actions/alertActions';
import { getVisibleModal } from '../../../redux/reducer/alertsReducer';
import { connect } from 'react-redux';
import CreateANewAirdrop from '../../modules/airdrop/createANewAirdrop';
import { verificationModal } from '../../modules/Utils/utilModals';
import SingleAirdropSubmissions from '../../modules/airdrop/SingleAirdropSubmissions';

class Lists extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      key: 'airdrops',
      airdrops: [],
      airdropsPagination: {
        pageSize: 10,
        rowCount: 0,
        page: 1,
        pageCount: 1,
      },
    };
  }

  componentDidMount() {
    this.getAllAirdropsWithPagination();
  }

  createANewAirdrop(form) {
    this.props.showModal({
      toggle: () => {
        console.log('TOGGLED');
      },
      onCancel: () => {
        console.log('CANCELLED');
      },
      onConfirm: (payload) => {
        let AD = new Airdrops();
        if (form?.id) {
          payload = Object.keys(payload)
            .filter((e) => payload[e] !== form[e])
            .map((e) => {
              return { [e]: payload[e] };
            })
            .reduce((prev, curr) => {
              return { ...prev, ...curr };
            }, {});
          payload['id'] = form?.id;
        }
        (form?.id ? AD.updateAirdrop(payload) : AD.createAirdrop(payload))
          .then((data) => {
            console.log(data);
            this.props.closeModal();
            this.props.showModal({
              header: 'Operation success',
              body: <div>A new airdrop has been added</div>,
            });
          })
          .catch((err) => {
            this.props.closeModal();
            this.props.showModal({
              header: 'Operation Failed',
              body: <div>Please refresh the page and try again</div>,
            });
          });
      },
      fade: true,
      header: 'Adding new Airdrop:',
      globalModalStyle: {
        width: '50vw',
        maxWidth: '40vw',
        minWidth: '300px',
        margin: 'auto',
      },
      body: <CreateANewAirdrop form={form} />,
      buttons: [
        {
          title: 'Save',
          color: 'primary',
          loaderOnClick: true,
          onClick: (modalData) => {
            let payload = modalData.ModalActionsExtra.refs.saveNewAirdrop();
            return modalData.ModalActionsExtra.modalConfig.onConfirm(payload);
          },
          class: '',
        },
      ],
    });
  }

  paginateTo(page, pageSize) {
    this.setState(
      {
        airdropsPagination: {
          ...this.state.airdropsPagination,
          page,
          pageSize,
        },
      },
      () => {
        this.getAllAirdropsWithPagination();
      },
    );
  }

  getAllAirdropsWithPagination() {
    const { airdropsPagination } = this.state;

    let AD = new Airdrops();
    return AD.getAirdrops(null, {
      page: airdropsPagination.page,
      pageSize: airdropsPagination.pageSize,
    })
      .then((response) => {
        this.setState({
          airdrops: response?.data?.data,
          airdropsPagination: response?.data?.pagination,
        });
      })
      .catch((err) => {
        console.log(err);
      });
  }

  getNewButtonText() {
    switch (this.state.key) {
      case 'airdrops': {
        return {
          text: 'Create a new airdrop',
        };
      }
      default: {
        console.log('default');
      }
    }
  }

  createNewEntity() {
    const { key } = this.state;
    console.log(key);
    switch (key) {
      case 'airdrops': {
        this.createANewAirdrop();
        break;
      }
      default: {
        console.log('default');
      }
    }
  }

  finishAirdrop(airdropId) {
    if (!airdropId) return;
    let AD = new Airdrops();
    AD.finishAirdrop(airdropId)
      .then((data) => {
        console.log(data);
        this.props.showModal({
          header: 'Operation success',
          body: <div>A new token has been added</div>,
        });
      })
      .catch((err) => {
        this.props.showModal({
          header: 'Operation Failed',
          body: <div>Please refresh the page and try again</div>,
        });
      });
  }

  openSubmissionsModal(airdropName, airdropId) {
    this.props.showModal({
      header: `Showing all Submissions of ${airdropName}`,
      body: <SingleAirdropSubmissions airdropId={airdropId} />,
      globalModalStyle: {
        width: '50vw',
        maxWidth: '60vw',
        minWidth: '700px',
        margin: 'auto',
      },
      fade: true,
    });
  }

  render() {
    return (
      <div className={'page-airdrops reduceWidth25'}>
        <div className={`card card-custom `}>
          {/* begin::Header */}
          <div className='card-header border-0 py-5'>
            <h3 className='card-title align-items-start flex-column'>
              <span className='card-label font-weight-bolder text-dark'>
                Airdrops List
              </span>
              <span className='text-muted mt-3 font-weight-bold font-size-sm'>
                Management page for Airdrops
              </span>
            </h3>
            <div className='card-toolbar'>
              <button
                onClick={() => this.createNewEntity()}
                className='btn btn-success font-weight-bolder font-size-sm'>
                <span className='svg-icon svg-icon-md svg-icon-white'>
                  <SVG
                    src={toAbsoluteUrl(
                      '/media/svg/icons/Communication/Add-user.svg',
                    )}
                    className='h-50 align-self-center'></SVG>
                </span>
                {this.getNewButtonText()?.text}
              </button>
            </div>
          </div>
          {/* end::Header */}

          {/* begin::Body */}
          <div className='card-body py-0'>
            {/* begin::Table */}
            <Tabs
              id='controlled-tab-example'
              activeKey={this.state.key}
              onSelect={(key) => this.setState({ key })}>
              <Tab eventKey='airdrops' title='Airdrops'>
                <div className='table-responsive p-1'>
                  <table
                    className='table table-head-custom table-vertical-center'
                    id='kt_advance_table_widget_1'>
                    <thead>
                      <tr className='text-left'>
                        <th className='pl-0' style={{ width: 20 }}>
                          <label className='checkbox checkbox-lg checkbox-inline'>
                            <input type='checkbox' value='1' />
                            <span></span>
                          </label>
                        </th>
                        <th className='pr-0' style={{ width: 50 }}>
                          Id
                        </th>
                        <th className='pr-0' style={{ width: 80 }}>
                          Name
                        </th>
                        <th style={{ minWidth: 200 }}>Description</th>
                        <th style={{ minWidth: 150 }}>Deadline</th>
                        <th style={{ minWidth: 150 }}>Created at</th>
                        <th
                          className='pr-0 text-center'
                          style={{ minWidth: 150 }}>
                          Number of winners
                        </th>
                        <th
                          className='pr-0 text-center'
                          style={{ minWidth: 150 }}>
                          Number current registrations
                        </th>
                        <th className={'text-center'}>Token</th>
                        <th className={'text-center'} style={{ minWidth: 150 }}>
                          Actions
                        </th>
                      </tr>
                    </thead>
                    <tbody>
                      {this.state.airdrops?.map((e, i) => {
                        return (
                          <tr key={i}>
                            <td className='pl-0'>
                              <label className='checkbox checkbox-lg checkbox-inline'>
                                <input type='checkbox' value='1' />
                                <span></span>
                              </label>
                            </td>
                            <td className='pr-0'>
                              <span className='text-muted font-weight-bold text-muted d-block'>
                                {e.id}
                              </span>
                            </td>
                            <td className='pr-0'>
                              <span className='text-muted font-weight-bold text-muted d-block'>
                                {e.name}
                              </span>
                            </td>
                            <td className='pl-0'>
                              <span className='text-muted font-weight-bold text-muted d-block'>
                                {e.description}
                              </span>
                            </td>
                            <td>
                              <span className='text-dark-75 font-weight-bolder d-block font-size-lg'>
                                {moment(e.deadline).fromNow()}
                              </span>
                              <span className='text-muted font-weight-bold'>
                                {moment(e.deadline).format('YYYY-MM-D.H:m')}
                              </span>
                            </td>
                            <td>
                              <span className='text-dark-75 font-weight-bolder d-block font-size-lg'>
                                {moment(e.created_at).fromNow()}
                              </span>
                              <span className='text-muted font-weight-bold'>
                                {moment(e.created_at).format('YYYY-MM-D.H:m')}
                              </span>
                            </td>
                            <td>
                              <span className='text-dark-75 font-weight-bolder d-block font-size-lg text-center'>
                                {e.number_of_winners}
                              </span>
                            </td>
                            <td>
                              <span className='text-dark-75 font-weight-bolder d-block font-size-lg text-center'>
                                {e.wallets?.length ?? 0}
                              </span>
                            </td>
                            <td className='token-td'>
                              <div className='token-td-box'>
                                <img
                                  src={toPublicBackendUrl(e.token?.icon)}
                                  className='h-100 align-self-end'
                                  alt=''
                                />
                                <span>{e.token?.name}</span>
                              </div>
                            </td>
                            <td className='action-td'>
                              <a
                                title='Edit'
                                className='action-edit'
                                onClick={() => this.createANewAirdrop(e)}>
                                <span>
                                  <SVG
                                    src={toAbsoluteUrl(
                                      '/media/svg/icons/Communication/Write.svg',
                                    )}
                                  />
                                </span>
                              </a>
                              <a
                                title='GetSubmissions'
                                className='action-submission'
                                onClick={() =>
                                  this.openSubmissionsModal(e.name, e.id)
                                }>
                                <span>
                                  <i className={'fa fa-list'} />
                                </span>
                              </a>
                              <a
                                title='FinishAirdrop'
                                className='action-finish'
                                onClick={() =>
                                  verificationModal(
                                    () => this.finishAirdrop(e.id),
                                    {
                                      body:
                                        'You are about to finish this airdrop manually',
                                    },
                                  )
                                }>
                                <span>
                                  <SVG
                                    src={toAbsoluteUrl(
                                      '/media/svg/icons/Communication/Flag.svg',
                                    )}
                                  />
                                </span>
                              </a>
                            </td>
                          </tr>
                        );
                      })}
                    </tbody>
                  </table>
                </div>
              </Tab>
            </Tabs>
            {/* end::Table */}
          </div>
          {/* end::Body */}
        </div>

        <div className='page-pagination-box d-flex justify-content-between align-items-center flex-wrap'>
          <div className='d-flex align-items-center py-3'>
            <select
              defaultValue={'10'}
              className='form-control form-control-sm font-weight-bold mr-4 border-0 bg-light'
              onChange={(event) =>
                this.paginateTo(
                  this.state.airdropsPagination?.page - 1,
                  event.target.value,
                )
              }
              style={{ width: 75 }}>
              <option value='10'>10</option>
              <option value='20'>20</option>
              <option value='30'>30</option>
              <option value='50'>50</option>
              <option value='100'>100</option>
            </select>
            <span className='display-text'>
              Displaying {this.state.airdropsPagination?.pageSize} of{' '}
              {this.state.airdropsPagination?.rowCount} records
            </span>
          </div>
          <div className='d-flex flex-wrap py-2 mr-3'>
            <button
              disabled={this.state.airdropsPagination?.page <= 1}
              onClick={() =>
                this.paginateTo(1, this.state.airdropsPagination?.pageSize)
              }
              className='btn btn-icon btn-sm btn-light mr-2 my-1'>
              <i className='ki ki-bold-double-arrow-back icon-xs'></i>
            </button>
            <button
              disabled={this.state.airdropsPagination?.page <= 1}
              onClick={() =>
                this.paginateTo(
                  this.state.airdropsPagination?.page - 1,
                  this.state.airdropsPagination?.pageSize,
                )
              }
              className='btn btn-icon btn-sm btn-light mr-2 my-1'>
              <i className='ki ki-bold-arrow-back icon-xs'></i>
            </button>

            {this.state.airdropsPagination?.page - 1 > 0 && (
              <a
                onClick={() =>
                  this.paginateTo(
                    this.state.airdropsPagination?.page - 1,
                    this.state.airdropsPagination?.pageSize,
                  )
                }
                className='btn btn-icon btn-sm border-0 btn-light mr-2 my-1'>
                {' '}
                {this.state.airdropsPagination?.page - 1}
              </a>
            )}
            <button className='btn btn-icon btn-sm border-0 btn-light btn-hover-primary active mr-2 my-1'>
              {this.state.airdropsPagination?.page}
            </button>
            {this.state.airdropsPagination?.page + 1 <=
              this.state.airdropsPagination?.pageCount && (
              <a
                onClick={() =>
                  this.paginateTo(
                    this.state.airdropsPagination?.page + 1,
                    this.state.airdropsPagination?.pageSize,
                  )
                }
                className='btn btn-icon btn-sm border-0 btn-light mr-2 my-1'>
                {this.state.airdropsPagination?.page + 1}
              </a>
            )}

            <button
              disabled={
                this.state.airdropsPagination?.page >=
                this.state.airdropsPagination?.pageSize
              }
              onClick={() =>
                this.paginateTo(
                  this.state.airdropsPagination?.page + 1,
                  this.state.airdropsPagination?.pageSize,
                )
              }
              className='btn btn-icon btn-sm btn-light mr-2 my-1'>
              <i className='ki ki-bold-arrow-next icon-xs'></i>
            </button>
            <button
              disabled={
                this.state.airdropsPagination?.page >=
                this.state.airdropsPagination?.pageSize
              }
              onClick={() =>
                this.paginateTo(
                  this.state.airdropsPagination?.pageCount,
                  this.state.airdropsPagination?.pageSize,
                )
              }
              className='btn btn-icon btn-sm btn-light mr-2 my-1'>
              <i className='ki ki-bold-double-arrow-next icon-xs'></i>
            </button>
          </div>
        </div>
      </div>
    );
  }
}

const mapRouterStateToProps = (state) => ({
  globalModal: state.globalModal,
  visibleModal: getVisibleModal(state.alerts),
});

let GLists = connect(mapRouterStateToProps, {
  closeModal: closeAlert,
  showModal: showAlert,
})(Lists);

export default GLists;
