import axios from 'axios'
import {ErrorHandler} from './'
import store from "../../../../redux/store";
const axiosInstance = axios.create();

let requestForgerer = {

  /**
   * {
   *     method : [GET, POST, PUT, DELETE]
   * }   * Will return a finalized request
   */
  forge({method,url,payload,config = {}} = {}){

      switch(method){
        case 'GET' :{
          return new ErrorHandler(
            axiosInstance.get(url,
              {
                headers :{
                  "authorization" : store.getState().auth?.token
                },
                ...config,
                ...(payload ? {params: payload}: {})
              })
          ) ;
        }
        case 'POST' :{
          return new ErrorHandler(
          axiosInstance.post(url,payload,{
            headers :{
              "authorization" : store.getState().auth?.token
            },
            ...config,
          }));
        }
        case 'PUT' : {
          return  new ErrorHandler(
            axiosInstance.put(url,payload,{
            headers :{
              "authorization" : store.getState().auth?.token
            },
              ...config,
          }));
        }
        case 'DELETE' :{
          return new ErrorHandler(axiosInstance.delete(url,{
            headers :{
              "authorization" : store.getState().auth?.token
            },
            ...config,
          }));
        }
        default :{
          return new ErrorHandler(axiosInstance.get(url,{
            headers :{
              "authorization" : store.getState().auth?.token
            },
            ...config,
          }));
        }
      }
  }


}




export default requestForgerer;
export const localAxiosInstance = axiosInstance;


