/* eslint-disable jsx-a11y/anchor-is-valid */
import React from 'react';
import { Tab, Tabs } from 'react-bootstrap';
import * as moment from 'moment';
import SVG from 'react-inlinesvg';
import { toAbsoluteUrl, toPublicBackendUrl } from '../../../_metronic/_helpers';
import { Launchpads } from '../../services/Requests';
import { closeAlert, showAlert } from '../../../redux/actions/alertActions';
import { getVisibleModal } from '../../../redux/reducer/alertsReducer';
import { connect } from 'react-redux';
import CreateANewLaunchpad from '../../modules/launchpad/createANewLaunchpad';
import SingleLaunchpadSubmissions from '../../modules/launchpad/SingleLaunchpadSubmissions';

class LaunchpadsPage extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      key: 'launchpads',
      launchpads: [],
      launchpadsPagination: {
        pageSize: 10,
        rowCount: 0,
        page: 1,
        pageCount: 1,
      },
    };
  }

  componentDidMount() {
    this.getAllLaunchpadsWithPagination();
  }

  createANewLaunchpad(form) {
    this.props.showModal({
      toggle: () => {
        console.log('TOGGLED');
      },
      onCancel: () => {
        console.log('CANCELLED');
      },
      onConfirm: (payload) => {
        let LA = new Launchpads();
        if (!form?.id) {
          payload['status'] = 0;
        }
        if (form?.id) {
          payload = Object.keys(payload)
            .filter((e) => payload[e] !== form[e] && payload[e] != null)
            .map((e) => {
              return { [e]: payload[e] };
            })
            .reduce((prev, curr) => {
              return { ...prev, ...curr };
            }, {});
          payload['id'] = form?.id;
        }
        (form?.id ? LA.updateALaunchpad(payload) : LA.createLaunchpad(payload))
          .then((data) => {
            console.log(data);
            this.props.closeModal();
            this.props.showModal({
              header: 'Operation success',
              body: (
                <div>
                  {form?.id
                    ? 'Launchpad Updated'
                    : 'Launchpad was sent to the queue for processing'}
                </div>
              ),
            });
          })
          .catch((err) => {
            this.props.closeModal();
            this.props.showModal({
              header: 'Operation Failed',
              body: <div>Please refresh the page and try again</div>,
            });
          });
      },
      fade: true,
      header: form?.id ? 'Updating a Launchpad' : 'Adding new Launchpad:',
      globalModalStyle: {
        width: form?.id ? '65vw' : '40vw',
        maxWidth: form?.id ? '65vw' : '40vw',
        minWidth: '300px',
        margin: 'auto',
        maxHeight: '900px',
      },
      body: (
        <CreateANewLaunchpad
          style={{ height: '100%', overflow: 'scroll' }}
          form={form}
        />
      ),
      buttons: [
        {
          title: 'Save',
          color: 'primary',
          loaderOnClick: true,
          onClick: (modalData) => {
            console.log(modalData);
            let payload = modalData.ModalActionsExtra.refs.saveNewLaunchpad();
            console.log(payload);
            return modalData.ModalActionsExtra.modalConfig.onConfirm(payload);
          },
          class: '',
        },
      ],
    });
  }

  paginateTo(page, pageSize) {
    this.setState(
      {
        launchpadsPagination: {
          ...this.state.launchpadsPagination,
          page,
          pageSize,
        },
      },
      () => {
        this.getAllLaunchpadsWithPagination();
      },
    );
  }

  getAllLaunchpadsWithPagination() {
    const { launchpadsPagination } = this.state;

    let LA = new Launchpads();
    return LA.getLaunchPads(null, {
      page: launchpadsPagination.page,
      pageSize: launchpadsPagination.pageSize,
    })
      .then((response) => {
        this.setState({
          launchpads: response?.data?.data,
          launchpadsPagination: response?.data?.pagination,
        });
      })
      .catch((err) => {
        console.log(err);
      });
  }

  getNewButtonText() {
    switch (this.state.key) {
      case 'launchpads': {
        return {
          text: 'Create a new launchpad',
        };
      }
      default:
        return {
          text: '',
        };
    }
  }

  createNewEntity() {
    const { key } = this.state;
    console.log(key);
    switch (key) {
      case 'launchpads': {
        this.createANewLaunchpad();
        break;
      }
      default:
        break;
    }
  }

  getStageRepresentation(launchpad) {
    switch (launchpad.status) {
      case 0: {
        return {
          name: 'Presale',
          cap: launchpad?.presale_cap,
          sold: launchpad?.presale_amount_sold,
          price: launchpad?.presale_token_value,
        };
      }
      case 1: {
        return {
          name: 'Initial Launch Offer',
          sold: launchpad?.ilo_amount_sold,
          cap: launchpad?.ilo_cap,
          price: launchpad?.ilo_token_value,
        };
      }
      case 2: {
        return {
          name: 'Completed',
          sold: 'N/A',
          cap: 'N/A',
          price: 'N/A',
        };
      }
      default: {
        return {
          name: 'Unknown',
          sold: 'N/A',
          cap: 'N/A',
          price: 'N/A',
        };
      }
    }
  }

  openSubmissionsModal(launchpadName, launchpadId) {
    this.props.showModal({
      header: `Showing all Submissions of ${launchpadName}`,
      body: <SingleLaunchpadSubmissions launchpadId={launchpadId} />,
      globalModalStyle: {
        width: '50vw',
        maxWidth: '60vw',
        minWidth: '700px',
        margin: 'auto',
      },
      fade: true,
    });
  }

  render() {
    return (
      <div className={'p-1 reduceWidth25 page-launchpads'}>
        <div className={`card card-custom `}>
          {/* begin::Header */}
          <div className='card-header border-0 py-5'>
            <h3 className='card-title align-items-start flex-column'>
              <span className='card-label font-weight-bolder text-dark'>
                Launchpads
              </span>
              <span className='text-muted mt-3 font-weight-bold font-size-sm'>
                Management page for Launchpads
              </span>
            </h3>
            <div className='card-toolbar'>
              <button
                onClick={() => this.createNewEntity()}
                className='btn btn-success font-weight-bolder font-size-sm'>
                <span className='svg-icon svg-icon-md svg-icon-white'>
                  <SVG
                    src={toAbsoluteUrl(
                      '/media/svg/icons/Communication/Add-user.svg',
                    )}
                    className='h-50 align-self-center'></SVG>
                </span>
                {this.getNewButtonText()?.text}
              </button>
            </div>
          </div>
          {/* end::Header */}

          {/* begin::Body */}
          <div className='card-body py-0'>
            {/* begin::Table */}
            <Tabs
              id='controlled-tab-example'
              activeKey={this.state.key}
              onSelect={(key) => this.setState({ key })}>
              <Tab eventKey='launchpads' title='Launchpads'>
                <div className='table-responsive p-1'>
                  <table
                    className='table table-head-custom table-vertical-center'
                    id='kt_advance_table_widget_1'>
                    <thead>
                      <tr className='text-left'>
                        <th className='pl-0' style={{ width: 20 }}>
                          <label className='checkbox checkbox-lg checkbox-inline'>
                            <input type='checkbox' value='1' />
                            <span></span>
                          </label>
                        </th>
                        <th className='pr-0' style={{ width: 50 }}>
                          Id
                        </th>
                        <th className='pr-0' style={{ width: 80 }}>
                          Name
                        </th>
                        <th style={{ minWidth: 180, maxWidth: 200 }}>
                          Description
                        </th>
                        <th style={{ minWidth: 100 }}>Cap</th>
                        <th className='pr-0' style={{ minWidth: 150 }}>
                          Stage
                        </th>
                        <th style={{ minWidth: 150 }}>Stage Sold/Cap</th>
                        <th style={{ minWidth: 150 }}>Stage Price</th>
                        <th style={{ minWidth: 150 }}>Created at</th>
                        <th
                          className='pr-0 text-center'
                          style={{ minWidth: 150 }}>
                          Number of buyers
                        </th>
                        <th>Token</th>
                        <th>Actions</th>
                      </tr>
                    </thead>
                    <tbody>
                      {this.state.launchpads?.map((e) => {
                        return (
                          <tr key={e?.id}>
                            <td className='pl-0'>
                              <label className='checkbox checkbox-lg checkbox-inline'>
                                <input type='checkbox' value='1' />
                                <span></span>
                              </label>
                            </td>
                            <td className='pr-0'>
                              <span className='text-muted font-weight-bold text-muted d-block'>
                                {e.id}
                              </span>
                            </td>
                            <td className='pr-0'>
                              <span className='text-muted font-weight-bold text-muted d-block'>
                                {e.name}
                              </span>
                            </td>
                            <td className='pl-0'>
                              <span className='text-muted font-weight-bold text-muted d-block'>
                                {e.description}
                              </span>
                            </td>
                            <td>
                              <span className='text-dark-75 font-weight-bolder d-block font-size-lg'>
                                {Number(e.cap).toLocaleString()} Tokens
                              </span>
                            </td>
                            <td>
                              <span className='text-dark-75 font-weight-bolder d-block font-size-lg'>
                                {this.getStageRepresentation(e).name}
                              </span>
                            </td>
                            <td>
                              <span className='text-dark-75 font-weight-bolder d-block font-size-lg'>
                                {this.getStageRepresentation(e).sold} /{' '}
                                {this.getStageRepresentation(e).cap}
                              </span>
                            </td>
                            <td>
                              <span className='text-dark-75 font-weight-bolder d-block font-size-lg'>
                                {this.getStageRepresentation(e).price}
                              </span>
                            </td>
                            <td>
                              <span className='text-dark-75 font-weight-bolder d-block font-size-lg'>
                                {moment(e.created_at).fromNow()}
                              </span>
                              <span className='text-muted font-weight-bold'>
                                {moment(e.created_at).format('YYYY-MM-D.H:m')}
                              </span>
                            </td>
                            <td>
                              <span className='text-dark-75 font-weight-bolder d-block font-size-lg'>
                                {e.wallets?.length ?? 0}
                              </span>
                            </td>
                            <td className='pr-0 token-td'>
                              <div className='token-td-box'>
                                <div className='symbol symbol-50 symbol-light mt-1'>
                                  <span className='symbol-label'>
                                    <img
                                      src={toPublicBackendUrl(e.token?.icon)}
                                      alt=''
                                    />
                                  </span>
                                </div>
                                <span className='text-dark-75 font-weight-bolder d-block font-size-lg text-center'>
                                  {e.token?.name}
                                </span>
                              </div>
                            </td>
                            <td className='action-td'>
                              <a
                                title='Edit'
                                className='btn btn-icon btn-light btn-hover-primary btn-sm mx-3 action-edit'
                                onClick={() => this.createANewLaunchpad(e)}>
                                <span className='svg-icon svg-icon-md svg-icon-primary'>
                                  <SVG
                                    src={toAbsoluteUrl(
                                      '/media/svg/icons/Communication/Write.svg',
                                    )}
                                  />
                                </span>
                              </a>
                              <a
                                title='GetSubmissions'
                                className='action-submission btn btn-icon btn-light btn-hover-primary btn-sm mx-3'
                                onClick={() =>
                                  this.openSubmissionsModal(e?.name, e?.id)
                                }>
                                <span className='svg-icon svg-icon-md svg-icon-primary'>
                                  <i className={'fa fa-list'} />
                                </span>
                              </a>
                            </td>
                          </tr>
                        );
                      })}
                    </tbody>
                  </table>
                </div>
              </Tab>
            </Tabs>
            {/* end::Table */}
          </div>
          {/* end::Body */}
        </div>
        <div className='page-pagination-box d-flex justify-content-between align-items-center flex-wrap'>
          <div className='d-flex align-items-center py-3'>
            <select
              defaultValue={'10'}
              className='form-control form-control-sm font-weight-bold mr-4 border-0 bg-light'
              onChange={(event) =>
                this.paginateTo(
                  this.state.launchpadsPagination?.page - 1,
                  event.target.value,
                )
              }
              style={{ width: 75 }}>
              <option value='10'>10</option>
              <option value='20'>20</option>
              <option value='30'>30</option>
              <option value='50'>50</option>
              <option value='100'>100</option>
            </select>
            <span className='display-text'>
              Displaying{' '}
              {Math.min(
                this.state.launchpadsPagination?.pageSize *
                  this.state.launchpadsPagination?.page,
                this.state.launchpadsPagination?.rowCount,
              )}{' '}
              of {this.state.launchpadsPagination?.rowCount} records
            </span>
          </div>
          <div className='d-flex flex-wrap py-2 mr-3'>
            <button
              disabled={this.state.launchpadsPagination?.page >= 1}
              onClick={() =>
                this.paginateTo(1, this.state.launchpadsPagination?.pageSize)
              }
              className='btn btn-icon btn-sm btn-light mr-2 my-1'>
              <i className='ki ki-bold-double-arrow-back icon-xs'></i>
            </button>
            <button
              disabled={this.state.launchpadsPagination?.page >= 1}
              onClick={() =>
                this.paginateTo(
                  this.state.launchpadsPagination?.page - 1,
                  this.state.launchpadsPagination?.pageSize,
                )
              }
              className='btn btn-icon btn-sm btn-light mr-2 my-1'>
              <i className='ki ki-bold-arrow-back icon-xs'></i>
            </button>

            {this.state.launchpadsPagination?.page - 1 > 0 && (
              <a
                onClick={() =>
                  this.paginateTo(
                    this.state.launchpadsPagination?.page - 1,
                    this.state.launchpadsPagination?.pageSize,
                  )
                }
                className='btn btn-icon btn-sm border-0 btn-light mr-2 my-1'>
                {' '}
                {this.state.launchpadsPagination?.page - 1}
              </a>
            )}
            <button className='btn btn-icon btn-sm border-0 btn-light btn-hover-primary active mr-2 my-1'>
              {this.state.launchpadsPagination?.page}
            </button>
            {this.state.launchpadsPagination?.page + 1 <=
              this.state.launchpadsPagination?.pageCount && (
              <a
                onClick={() =>
                  this.paginateTo(
                    this.state.launchpadsPagination?.page + 1,
                    this.state.launchpadsPagination?.pageSize,
                  )
                }
                className='btn btn-icon btn-sm border-0 btn-light mr-2 my-1'>
                {this.state.launchpadsPagination?.page + 1}
              </a>
            )}

            <button
              disabled={
                this.state.launchpadsPagination?.page >=
                this.state.launchpadsPagination?.pageSize
              }
              onClick={() =>
                this.paginateTo(
                  this.state.launchpadsPagination?.page + 1,
                  this.state.launchpadsPagination?.pageSize,
                )
              }
              className='btn btn-icon btn-sm btn-light mr-2 my-1'>
              <i className='ki ki-bold-arrow-next icon-xs'></i>
            </button>
            <button
              disabled={
                this.state.launchpadsPagination?.page >=
                this.state.launchpadsPagination?.pageSize
              }
              onClick={() =>
                this.paginateTo(
                  this.state.launchpadsPagination?.pageCount,
                  this.state.launchpadsPagination?.pageSize,
                )
              }
              className='btn btn-icon btn-sm btn-light mr-2 my-1'>
              <i className='ki ki-bold-double-arrow-next icon-xs'></i>
            </button>
          </div>
        </div>
      </div>
    );
  }
}

const mapRouterStateToProps = (state) => ({
  globalModal: state.globalModal,
  visibleModal: getVisibleModal(state.alerts),
});

let GLists = connect(mapRouterStateToProps, {
  closeModal: closeAlert,
  showModal: showAlert,
})(LaunchpadsPage);

export default GLists;
