/* eslint-disable jsx-a11y/anchor-is-valid */
import React from 'react';
import { Tab, Tabs } from 'react-bootstrap';
import * as moment from 'moment';
import SVG from 'react-inlinesvg';
import { toAbsoluteUrl, toPublicBackendUrl } from '../../../_metronic/_helpers';
import { Tokens } from '../../services/Requests';
import { closeAlert, showAlert } from '../../../redux/actions/alertActions';
import { getVisibleModal } from '../../../redux/reducer/alertsReducer';
import { connect } from 'react-redux';
import CreateANewToken from '../../modules/token/createANewToken';

class TokesPage extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      key: 'tokens',
      tokens: [],
      tokensPagination: {
        pageSize: 10,
        rowCount: 0,
        page: 1,
        pageCount: 1,
      },
    };
  }

  componentDidMount() {
    this.getAllTokensWithPagination();
  }

  createANewToken(form) {
    this.props.showModal({
      toggle: () => {
        console.log('TOGGLED');
      },
      onCancel: () => {
        console.log('CANCELLED');
      },
      onConfirm: (payload) => {
        let TA = new Tokens();
        console.log(payload);
        (form?.id ? TA.updateANewToken(payload) : TA.createAnewToken(payload))
          .then((data) => {
            console.log(data);
            this.props.closeModal();
            this.props.showModal({
              header: 'Operation success',
              body: <div>A new token has been added</div>,
            });
          })
          .catch((err) => {
            this.props.closeModal();
            this.props.showModal({
              header: 'Operation Failed',
              body: <div>Please refresh the page and try again</div>,
            });
          });
      },
      fade: true,
      header: 'Adding new token:',
      globalModalStyle: {
        width: '50vw',
        maxWidth: '40vw',
        minWidth: '300px',
        margin: 'auto',
      },
      body: <CreateANewToken form={form} />,
      buttons: [
        {
          title: 'Save',
          color: 'primary',
          loaderOnClick: true,
          onClick: (modalData) => {
            console.log(modalData);
            let payload = modalData.ModalActionsExtra.refs.saveANewToken();
            console.log(payload);
            return modalData.ModalActionsExtra.modalConfig.onConfirm(payload);
          },
          class: '',
        },
      ],
    });
  }

  paginateTo(page, pageSize) {
    this.setState(
      {
        tokensPagination: {
          ...this.state.tokensPagination,
          page,
          pageSize,
        },
      },
      () => {
        this.getAllTokensWithPagination();
      },
    );
  }

  getAllTokensWithPagination() {
    const { tokensPagination } = this.state;

    let TA = new Tokens();
    return TA.getAllTokens(null, {
      page: tokensPagination.page,
      pageSize: tokensPagination.pageSize,
    })
      .then((response) => {
        this.setState({
          tokens: response?.data?.data,
          tokensPagination: response?.data?.pagination,
        });
      })
      .catch((err) => {
        console.log(err);
      });
  }

  getNewButtonText() {
    switch (this.state.key) {
      case 'tokens': {
        return {
          text: 'Create a new token',
        };
      }
      default: {
        console.log('No button text found');
      }
    }
  }

  createNewEntity() {
    const { key } = this.state;
    console.log(key);
    switch (key) {
      case 'tokens': {
        this.createANewToken();
        break;
      }
      default: {
        console.log('No entity found');
      }
    }
  }

  render() {
    return (
      <div className={'p-1 reduceWidth25 page-tokens'}>
        <div className={`card card-custom `}>
          {/* begin::Header */}
          <div className='card-header border-0 py-5'>
            <h3 className='card-title align-items-start flex-column'>
              <span className='card-label font-weight-bolder text-dark'>
                Tokens
              </span>
              <span className='text-muted mt-3 font-weight-bold font-size-sm'>
                Management page for Tokens
              </span>
            </h3>
            <div className='card-toolbar'>
              <button
                onClick={() => this.createNewEntity()}
                className='btn btn-success font-weight-bolder font-size-sm'>
                <span className='svg-icon svg-icon-md svg-icon-white'>
                  <SVG
                    src={toAbsoluteUrl(
                      '/media/svg/icons/Communication/Add-user.svg',
                    )}
                    className='h-50 align-self-center'></SVG>
                </span>
                {this.getNewButtonText()?.text}
              </button>
            </div>
          </div>
          {/* end::Header */}

          {/* begin::Body */}
          <div className='card-body py-0'>
            {/* begin::Table */}
            <Tabs
              id='controlled-tab-example'
              activeKey={this.state.key}
              onSelect={(key) => this.setState({ key })}>
              <Tab eventKey='tokens' title='Tokens'>
                <div className='table-responsive p-1'>
                  <table
                    className='table table-head-custom table-vertical-center'
                    id='kt_advance_table_widget_1'>
                    <thead>
                      <tr className='text-left'>
                        <th className='pl-0' style={{ width: 20 }}>
                          <label className='checkbox checkbox-lg checkbox-inline'>
                            <input type='checkbox' value='1' />
                            <span></span>
                          </label>
                        </th>
                        <th className='pr-0' style={{ width: 50 }}>
                          Id
                        </th>
                        <th className='pr-0' style={{ width: 100 }}>
                          Name
                        </th>
                        <th style={{ minWidth: 50, maxWidth: 100 }}>Ticker</th>
                        <th style={{ minWidth: 120 }}>Address</th>
                        <th style={{ minWidth: 120 }}>Created at</th>
                        <th>Icon</th>
                        <th>Actions</th>
                      </tr>
                    </thead>
                    <tbody>
                      {this.state.tokens?.map((e) => {
                        return (
                          <tr key={e.id}>
                            <td className='pl-0'>
                              <label className='checkbox checkbox-lg checkbox-inline'>
                                <input type='checkbox' value='1' />
                                <span></span>
                              </label>
                            </td>
                            <td className='pr-0'>
                              <span className='text-muted font-weight-bold text-muted d-block'>
                                {e.id}
                              </span>
                            </td>
                            <td className='pr-0'>
                              <span className='text-muted font-weight-bold text-muted d-block'>
                                {e.name}
                              </span>
                            </td>
                            <td className='pl-0'>
                              <span className='text-muted font-weight-bold text-muted d-block'>
                                {e.ticker}
                              </span>
                            </td>
                            <td>
                              <span className='text-dark-75 font-weight-bolder d-block font-size-lg'>
                                {e.address}
                              </span>
                            </td>
                            <td>
                              <span className='text-dark-75 font-weight-bolder d-block font-size-lg'>
                                {moment(e.created_at).fromNow()}
                              </span>
                              <span className='text-muted font-weight-bold'>
                                {moment(e.created_at).format('YYYY-MM-D.H:m')}
                              </span>
                            </td>
                            <td className='pr-0 token-td'>
                              <div className='token-td-box'>
                                <div className='symbol symbol-50 symbol-light mt-1'>
                                  <span className='symbol-label'>
                                    <img
                                      src={toPublicBackendUrl(e.icon)}
                                      alt=''
                                    />
                                  </span>
                                </div>
                                <span className='text-dark-75 font-weight-bolder d-block font-size-lg text-center'>
                                  {e.name}
                                </span>
                              </div>
                            </td>
                            <td className='action-td'>
                              <a
                                href='#'
                                title='Edit'
                                className='action-edit btn btn-icon btn-light btn-hover-primary btn-sm mx-3'
                                onClick={() => this.createANewToken(e)}>
                                <span className='svg-icon svg-icon-md svg-icon-primary'>
                                  <SVG
                                    src={toAbsoluteUrl(
                                      '/media/svg/icons/Communication/Write.svg',
                                    )}
                                  />
                                </span>
                              </a>
                            </td>
                          </tr>
                        );
                      })}
                    </tbody>
                  </table>
                </div>
              </Tab>
            </Tabs>
            {/* end::Table */}
          </div>
          {/* end::Body */}
        </div>
        <div className='page-pagination-box d-flex justify-content-between align-items-center flex-wrap'>
          <div className='d-flex align-items-center py-3'>
            <select
              defaultValue={'10'}
              className='form-control form-control-sm font-weight-bold mr-4 border-0 bg-light'
              onChange={(event) =>
                this.paginateTo(
                  this.state.tokensPagination?.page - 1,
                  event.target.value,
                )
              }
              style={{ width: 75 }}>
              <option value='10'>10</option>
              <option value='20'>20</option>
              <option value='30'>30</option>
              <option value='50'>50</option>
              <option value='100'>100</option>
            </select>
            <a href='#' className='btn btn-icon btn-sm btn-light mr-2 my-1'>
              <i className='ki ki-bold-double-arrow-next icon-xs'></i>
            </a>
            <span className='display-text'>
              Displaying{' '}
              {Math.min(
                this.state.tokensPagination?.pageSize *
                  this.state.tokensPagination?.page,
                this.state.tokensPagination?.rowCount,
              )}{' '}
              of {this.state.tokensPagination?.rowCount} records
            </span>
          </div>
          <div className='d-flex flex-wrap py-2 mr-3'>
            <button
              disabled={this.state.tokensPagination?.page >= 1}
              onClick={() =>
                this.paginateTo(1, this.state.tokensPagination?.pageSize)
              }
              className='btn btn-icon btn-sm btn-light mr-2 my-1'>
              <i className='ki ki-bold-double-arrow-back icon-xs'></i>
            </button>
            <button
              disabled={this.state.tokensPagination?.page >= 1}
              onClick={() =>
                this.paginateTo(
                  this.state.tokensPagination?.page - 1,
                  this.state.tokensPagination?.pageSize,
                )
              }
              className='btn btn-icon btn-sm btn-light mr-2 my-1'>
              <i className='ki ki-bold-arrow-back icon-xs'></i>
            </button>

            {this.state.tokensPagination?.page - 1 > 0 && (
              <a
                onClick={() =>
                  this.paginateTo(
                    this.state.tokensPagination?.page - 1,
                    this.state.tokensPagination?.pageSize,
                  )
                }
                className='btn btn-icon btn-sm border-0 btn-light mr-2 my-1'>
                {' '}
                {this.state.tokensPagination?.page - 1}
              </a>
            )}
            <button className='btn btn-icon btn-sm border-0 btn-light btn-hover-primary active mr-2 my-1'>
              {this.state.tokensPagination?.page}
            </button>
            {this.state.tokensPagination?.page + 1 <=
              this.state.tokensPagination?.pageCount && (
              <a
                onClick={() =>
                  this.paginateTo(
                    this.state.tokensPagination?.page + 1,
                    this.state.tokensPagination?.pageSize,
                  )
                }
                className='btn btn-icon btn-sm border-0 btn-light mr-2 my-1'>
                {this.state.tokensPagination?.page + 1}
              </a>
            )}

            <button
              disabled={
                this.state.tokensPagination?.page >=
                this.state.tokensPagination?.pageSize
              }
              onClick={() =>
                this.paginateTo(
                  this.state.tokensPagination?.page + 1,
                  this.state.tokensPagination?.pageSize,
                )
              }
              className='btn btn-icon btn-sm btn-light mr-2 my-1'>
              <i className='ki ki-bold-arrow-next icon-xs'></i>
            </button>
            <button
              disabled={
                this.state.tokensPagination?.page >=
                this.state.tokensPagination?.pageSize
              }
              onClick={() =>
                this.paginateTo(
                  this.state.tokensPagination?.pageCount,
                  this.state.tokensPagination?.pageSize,
                )
              }
              className='btn btn-icon btn-sm btn-light mr-2 my-1'>
              <i className='ki ki-bold-double-arrow-next icon-xs'></i>
            </button>
          </div>
        </div>
      </div>
    );
  }
}

const mapRouterStateToProps = (state) => ({
  globalModal: state.globalModal,
  visibleModal: getVisibleModal(state.alerts),
});

let GLists = connect(mapRouterStateToProps, {
  closeModal: closeAlert,
  showModal: showAlert,
})(TokesPage);

export default GLists;
