import {ADD_MODAL, REMOVE_MODAL} from '../types/alertTypes'


const addModal = modal => ({
    type: ADD_MODAL,
    modal
});

const removeModal = id => ({
    type: REMOVE_MODAL,
    id
});


export const showAlert = (modalProps) => dispatch => {

    if (!modalProps.id) {
        modalProps.id = '' + Date.now() + Math.random() + Math.random();
    }

    // auto-close on confirm
    if (modalProps.onConfirm) {
        const onConfirm = modalProps.onConfirm;
        modalProps.onConfirm = async (...args) => {
            await onConfirm(...args);
            closeAlert(modalProps.id)(dispatch);
        }
    } else {
        modalProps.onConfirm = () => closeAlert(modalProps.id)(dispatch);
    }

    // auto-close on cancel
    if (modalProps.onCancel) {
        const onCancel = modalProps.onCancel;
        modalProps.onCancel = async (...args) => {
            await onCancel(...args);
            closeAlert(modalProps.id)(dispatch);
        }
    } else {
        modalProps.onCancel = () => closeAlert(modalProps.id)(dispatch);
    }

    dispatch(addModal(modalProps));
};

export const closeAlert = (id) => dispatch => {
    dispatch(removeModal(id));
};

