import React from 'react';
import {Col, Form, Row} from "react-bootstrap";
import TextField from "@material-ui/core/TextField";
import {Divider} from "@material-ui/core";
import AsyncSelect from 'react-select/async'
import {Tokens} from '../../services/Requests'
import * as moment from 'moment';
import Switch from "react-switch";

class CreateANewAirdrop extends React.Component{

    constructor(props) {
        super(props);
        this.state={
            links: ['Twitter','Facebook', 'Telegram', 'Medium', 'Website', 'whitePaper', 'Video'],
            tokens:[],
            form:{
                token_id:null,
                ...(props.form ?? {})
            },
            formRef : "formRef",
        }
    }

    setFormValue(name, value){
        this.setState({
            form:{
                ...this.state.form,
                [name]: value
            }
        })
    }

    getTokens = (inputValue) => {
        return new Promise((resolve) => {
            let TK = new Tokens();
            TK.getAllTokens(inputValue).then((response)=>{
                resolve(response.data.data.map((token)=>{
                    return {
                        value: token.id,
                        label: token.name
                    }
                }))
            })
        })
    };

    saveNewAirdrop = (event) =>{
        let payload = Object.keys(this.refs[this.state.formRef].elements).filter((e)=>isNaN(parseInt(e))).map((e)=>{
            return {[e]:this.refs[this.state.formRef].elements[e].type === "number" ? parseFloat(this.refs[this.state.formRef].elements[e].value) : this.refs[this.state.formRef].elements[e].value}
        }).reduce((prev, curr)=>{return{...prev, ...curr}},{})
        payload["token_id"] = this.state.form.token_id;
        payload["hidden"] = this.state.form?.hidden;
        Object.keys(payload).filter(e => e.includes('react-select-')).forEach((e)=>{
            delete payload[e];
        })
        delete payload.token;
        if(this.state.form?.id){
            payload['id'] = this.state.form.id;
        }
        this.setState({
            form: payload
        })
        return payload

    }

    render(){
        const {links, form} = this.state;
        this.formRef = this.refs[this.state.formRef];
        return <Form ref={this.state.formRef} className={"w-100"} onSubmit={(ev) => this.saveNewAirdrop(ev)}>
            <Row>
                <Col>
                    <Form.Group controlId="name">
                        <Form.Label>Name*</Form.Label>
                        <Form.Control defaultValue={form.name} type="text" placeholder="Enter email" />
                        <Form.Text className="text-muted">
                            Input the airdrop Name
                        </Form.Text>
                    </Form.Group>

                    <Form.Group controlId="description">
                        <Form.Label>Description</Form.Label>
                        <Form.Control defaultValue={form.description} as="textarea" type="text" placeholder="description" />
                    </Form.Group>
                    <Form.Group controlId="tokenCap">
                        <Form.Label>Token Cap</Form.Label>
                        <Form.Control defaultValue={form.tokenCap} type="Number" placeholder="Token Cap" />
                    </Form.Group>
                    <Form.Group controlId="deadline">
                        <Form.Label>Deadline</Form.Label>
                        <TextField
                            id="deadline"
                            label="Airdrop deadline"
                            type="datetime-local"
                            defaultValue={moment(form.deadline).format('YYYY-MM-DDThh:mm')}
                            onChange={(event)=>{
                                this.setState({
                                    form:{
                                        deadline: event.target.value
                                    }
                                })
                            }}
                            style={{
                                display: "flex",
                                flexWrap: "wrap"
                            }}
                            InputLabelProps={{
                                shrink: true
                            }}
                        />
                    </Form.Group>
                    <Divider />
                    <h4 className={"mt-2"} >Token {form ?  <small>{form?.token?.name} ({form?.token?.id})</small>: ''}</h4>
                    <AsyncSelect
                        controlId="token_id"
                        isSearchable={true}
                        loadOptions={this.getTokens}
                        defaultValue={form.token_id}
                        defaultOptions
                        onChange={(event)=>{
                            this.setState({
                                form:{
                                    ...this.state.form,
                                    token_id: event.value
                                }
                            })
                        }}
                    >
                    </AsyncSelect>
                    <Form.Group controlId="number_of_winners">
                        <Form.Label>Number Of Winners</Form.Label>
                        <Form.Control defaultValue={form.number_of_winners} type="Number" placeholder="Number Of Winners" />
                    </Form.Group>
                    <Form.Group controlId="claim_amount">
                        <Form.Label>Claim Amount</Form.Label>
                        <Form.Control defaultValue={form.claim_amount} type="Number" placeholder="TWinner's claim Amount" />
                    </Form.Group>

                </Col>
                <Col>
                    <Form.Group controlId="hidden">
                        <Form.Label>Hide Airdrop</Form.Label>
                        <div className={"d-flex"}>
                            <span className={"my-auto"}>{this.state.form?.hidden ? "Airdrop hidden": "Airdrop visible"}</span>  <Switch className={"ml-auto"} onColor="#56D663" checked={this.state.form?.hidden===1}  onChange={(event)=>this.setFormValue('hidden', event?1:0)}/>
                        </div>
                    </Form.Group>

                    <h4 className={"mt-2"}>Trees</h4>
                    <Form.Group controlId="trees_to_be_planted">
                        <Form.Label>N. of trees to be planted</Form.Label>
                        <Form.Control defaultValue={form?.trees_to_be_planted} type="Number" placeholder="Number of trees to be planted after the airdrop ends" />
                    </Form.Group>


                    <Divider className={"my-2"} />

                    <h4>Social</h4>
                    {
                        links.map(e=>{
                            return <Form.Group key={e.toLowerCase()} controlId={e.toLowerCase()}>
                                <Form.Label>{e} link</Form.Label>
                                <Form.Control placeholder={`${e.toLowerCase()} link`} />
                            </Form.Group>
                        })
                    }
                </Col>
            </Row>
            {/* <Button variant="primary" type="submit">
                Save
            </Button> */}
        </Form>
    }
}

export default CreateANewAirdrop;
