import React from "react";
import store from '../../../redux/store'
import {showAlert} from "../../../redux/actions/alertActions";

export const verificationModal = (executingFunction, {header, body} = {}) =>{
    let config = {
        toggle: () => {
            console.log("TOGGLED");
        },
        onCancel: () => {
            console.log("CANCELLED");
        },
        onConfirm: async (payload) => {
            if(executingFunction){
                try{
                    await executingFunction(payload);
                    store.dispatch(showAlert({header: "Operation success", body: <div>Your operation was a success</div>}))
                }catch(e){
                    store.dispatch(showAlert({header: "Operation Error", body: <div>Your operation encountered an Error {e.data?.message ?? e.message}</div>}))
                }
            }

        },
        fade: true,
        header: header ? <div>{header}</div> : "Are you sure",
        globalModalStyle: {
            width: "50vw",
            maxWidth: "40vw",
            minWidth: "300px",
            margin: "auto"
        },
        body: body ? <div>{body}</div> : <p>Are you sure of your action ?</p>,
        buttons: [
            {
                title: "OK",
                color: "danger",
                loaderOnClick: true,
                onClick: 'onConfirm'
            },
            {
                title: "Cancel",
                color: "primary",
                loaderOnClick: true,
                onClick: 'onCancel',
            },
        ]
    }


    store.dispatch(showAlert(config));
}

