
import PropTypes from "prop-types";
import {connect} from "react-redux";
import * as React from 'react';
import GlobalModal from "./GlobalModal";
import {getAllModals, getVisibleModal} from "../../../redux/reducer/alertsReducer";
import {closeAlert} from '../../../redux/actions/alertActions';
import { loadProgressBar } from 'axios-progress-bar';
import 'axios-progress-bar/dist/nprogress.css';
import '../../assets/css/progressBar.css';



class GlobalAlerts extends React.Component {
    constructor(props) {
        super(props);

        this.state = {}
    }

    componentDidMount() {
        loadProgressBar()
    }

    render() {
        return (
            <div>
                {this.props?.allPageLoader ? (
                    <div className="preloader-wrap">
                        <div id="progress"></div>
                        <div className="preloader-box">
                        </div>
                    </div>
                ) : this.props?.allModals? this.props?.allModals?.map((e)=>{
                    return (
                        <GlobalModal key={e.id} modalConfig={e} closeModal={this.props.closeModal}/>
                    )
                }):""}
            </div>
        );
    }
}

GlobalAlerts.propTypes = {
    allPageLoader: PropTypes.any,
    visibleModal: PropTypes.any,
};

const mapRouterStateToProps = state => ({
    globalModal: state.globalModal,
    visibleModal: getVisibleModal(state.alerts),
    allModals: getAllModals(state.alerts)
});

let GlobalAlertComponent = connect(
    mapRouterStateToProps,
    {closeModal: closeAlert}
)(GlobalAlerts);

export default GlobalAlertComponent;
