import {SWITCH_SIDEBAR, GET_STATIC_VARS_ERROR, GET_STATIC_VARS} from '../types/utilTypes';
import Utils from "../../app/services/Requests/Requests/utils";

const fetchStaticVarsSuccess = (vars) =>({
    type: GET_STATIC_VARS,
    payload: vars
})

const fetchStaticVarsError = (error) =>({
    type: GET_STATIC_VARS_ERROR,
    payload: error
})

export const toggleSidebar = force => ({
    type: SWITCH_SIDEBAR,
    payload: force
})

export const getStaticVars = () => {
    return dispatch => {
        let UT = new Utils();
        UT.getAllVariables()
            .then(response => dispatch(fetchStaticVarsSuccess(response.data)))
            .catch(error => {
                dispatch(fetchStaticVarsError(error))
            });
    };
};
