import {forger} from '../RequestForgerer'
import URLS from './urls';


class Trees {

    getOneWalletTrees(address, {limit, offset}) {
        // prepare url
        let url = URLS.GET_TREES_OF_WALLET;
        console.log(url);
        // forge the url
        return forger.forge({
            method:'GET',
            url:url,
            payload:{
                address, limit, offset
            }
        });
    };


    getAllTrees({limit, offset}) {
        // prepare url
        let url = URLS.GET_ALL_TREES;
        console.log(url);
        // forge the url
        return forger.forge({
            method:'GET',
            url:url,
            payload:{
                limit, offset
            }
        });
    };

    plantATreeByWalletAddress(address){
        // prepare url
        let url = URLS.PLANT_A_TREE;
        console.log(url);
        // forge the url
        return forger.forge({
            method:'POST',
            url:url,
            payload:{
                address
            }
        });
    }
    plantASingleTree(tree_id){
        // prepare url
        let url = URLS.PLANT_A_TREE;
        console.log(url);
        // forge the url
        return forger.forge({
            method:'POST',
            url:url,
            payload:{
                tree_id
            }
        });
    }


}


export default Trees;
