import React from "react";
import {Launchpads} from "../../services/Requests";
import Table from "../common/table";
import {getVisibleModal} from "../../../redux/reducer/alertsReducer";
import {connect} from "react-redux";
import {closeAlert, showAlert} from "../../../redux/actions/alertActions";
import ShortFormAddress from "../common/shortFormAddress";

class SingleLaunchpadSubmissions extends React.Component{

    constructor(props) {
        super(props);
        this.tokenList = {
            "0x55d398326f99059fF775485246999027B3197955": {
                name: "USDT"
            },
            "bnb":{
                name: "BNB"
            }
        }
        this.state={
            airdropSubmissionsTableConfig: {},
            startingPage: 1,
            rowCount: null,
            initialPageSize: 10,
            tableHeaders: [
                {
                    id:"wallet",
                    classNames: "",
                    style:{
                        width:60
                    },
                    value : "Wallet",
                    cell: {
                        render: (value) =>{
                            return <ShortFormAddress address={value?.address}/>
                        }
                    }
                },
                {
                    id:"presale",
                    classNames: "",
                    style:{
                        width:50
                    },
                    value: "Presale / ilo",
                    cell:{
                        render: (value, row)=>{
                            return value ? 'Presale': 'Ilo'
                        }
                    }
                },
                {
                    id:"amount",
                    classNames: "",
                    style:{
                        width:50
                    },
                    value: "Investment amount",
                },
                {
                    id:"investment_token",
                    classNames: "",
                    style:{
                        width:50
                    },
                    value: "Investment Token",
                    cell:{
                        render: (value)=>{
                            if(!value) return this.tokenList['bnb']?.name;
                            return this.tokenList[value]?.name;
                        }
                    }
                },
                {
                    id:"actions",
                    classNames: "",
                    style:{
                        width:50
                    },
                    value: "Actions",
                    cell:{
                        render: (value, row)=>{
                            return ''
                        }
                    }
                },
            ],
            data: [],
        }
    }


    componentDidMount() {
        this.refreshLaunchpadSubmissions();
    }

    refreshLaunchpadSubmissions(){
        return this.getSingleLaunchpadSubmissions({limit:this.state.initialPageSize, offset:(this.state.startingPage - 1) * this.state.initialPageSize})
    }


    getSingleLaunchpadSubmissions({limit, offset}){
        let LA = new Launchpads();
        return LA.getLaunchpadSubmissions(this.props.launchpadId, {limit, offset}).then((response)=>{
            this.setState({
                data: response.data.data,
                rowCount: response.data.rowCount
            });
            return Promise.resolve(true);
        });
    }

    onPaginationChanges(pagination){
        this.getSingleLaunchpadSubmissions({
            limit: pagination.pageSize,
            offset: pagination.pageSize * (pagination.page - 1)
        })
    }


    render() {
        const {tableHeaders, data, rowCount} = this.state;

        return (
            <div>
                <Table
                    tableConfig={
                        {
                            headers: tableHeaders,
                            initialPageSize: 10,
                            startingPage: 1
                        }
                    }
                    rowCount={rowCount}
                    onPaginationChanges={(data)=>this.onPaginationChanges(data)}
                    data={data}
                />
            </div>
        );
    }
}


const mapRouterStateToProps = state => ({
    globalModal: state.globalModal,
    visibleModal: getVisibleModal(state.alerts),
});



export default connect(
    mapRouterStateToProps,
    {closeModal: closeAlert, showModal: showAlert}
)(SingleLaunchpadSubmissions);
