import {
    ADD_ALERT,
    REMOVE_ALERT,
    ADD_MODAL,
    REMOVE_MODAL,
    ADD_GLOBAL_SIDEBAR, REMOVE_GLOBAL_SIDEBAR
} from '../types/alertTypes'
const INITIAL_STATE = {
    alerts: [],
    modals: [],
    globalSidebars:[]
}


export const alerts = (state = INITIAL_STATE, action) => {
    switch (action.type) {
        case ADD_ALERT:
            return {
                ...state,
                alerts: [...state.alerts, action.alert]
            };
        case REMOVE_ALERT:
            return {
                ...state,
                alerts: [...state.alerts].filter(a => a.id !== action.id)
            };
        case ADD_MODAL:
            return {
                ...state,
                modals: [...state.modals, action.modal]
            };
        case REMOVE_MODAL:
            return {
                ...state,
                modals: [...state.modals].filter(a => a.id !== action.id)
            };
        case ADD_GLOBAL_SIDEBAR:
            return {
                ...state,
                globalSidebars: [...state.globalSidebars, action.globalSidebar]
            };
        case REMOVE_GLOBAL_SIDEBAR:
            return {
                ...state,
                globalSidebars: [...state.globalSidebars].filter(a => a.id !== action.id)
            };
        default:
            return state;
    }
}


export const getVisibleAlert = (state) => (state.alerts && state.alerts.length > 0) ? state.alerts[0] : null;
export const getVisibleModal = (state) => (state.modals && state.modals.length > 0) ? state.modals[0] : null;
export const getAllModals = (state) => (state.modals && state.modals.length > 0) ? state.modals : null;
export const getVisibleGlobalSidebar = (state) => (state.globalSidebars && state.globalSidebars.length > 0) ? state.globalSidebars[0] : null;

