import React, { Component } from "react";
import {
  Button,
  Col,
  Form,
  FormControl,
  InputGroup,
  Row,
  Tab,
  Tabs,
} from "react-bootstrap";
import { Divider } from "@material-ui/core";

import Utils from "../services/Requests/Requests/utils";

class SmartContractManagement extends Component {
  constructor(props) {
    super(props);
    this.state = {
      loading: false,
      key: "globalState",
      globalState: {
        totalUSDAirdropped: 0,
        totalUSDRaised: 0,
      }
    };
  }

  toggleLoading() {
    this.setState({
      loading: !this.state.loading,
    });
  }

  async getGlobalState() {
    const utils = new Utils();
    const globalState = await utils.getGlobalState();

    const state = {
      totalUSDAirdropped: globalState.data?.totalUSDAirdropped || 0,
      totalUSDRaised: globalState.data?.totalUSDRaised || 0,
    };

    this.setState({
        globalState: state,
    });
  }

  async updateGlobalState() {
    const utils = new Utils();
    console.log("updating global state", this.state.globalState);
    const updatedGlobalState = await utils.setGlobalState(this.state.globalState);

    const state = {
      totalUSDAirdropped: updatedGlobalState.data?.totalUSDAirdropped || 0,
      totalUSDRaised: updatedGlobalState.data?.totalUSDRaised || 0,
    }

    this.setState({
        globalState: state,
    });
  }

  componentDidMount() {
    this.getGlobalState();
  }

  render() {
    const {
      loading,
      key,
      globalState,
    } = this.state;

    return (
      <div className={"p-1"}>
        <div className={`card card-custom `}>
          <div className="card-header border-0 py-5">
            <h3 className="card-title align-items-start flex-column">
              <span className="card-label font-weight-bolder text-dark">
                Global State Management
              </span>
              <span className="text-muted mt-3 font-weight-bold font-size-sm">
                Management page for setting global variables and state
              </span>
            </h3>
            <div className="card-toolbar"></div>
          </div>
          <div className="card-body py-0">
            <Tabs
              id="controlled-tab-example"
              activeKey={key}
              onSelect={(key) => this.setState({ key })}
            >
              <Tab eventKey="globalState" title="Global state">
                <div className={"m-3"}>
                  <Row>
                    <Col>
                      <Form.Label htmlFor="basic-url">Total USD Airdropped</Form.Label>
                      <div className={"d-flex flex-column"}>
                        <InputGroup className={"d-flex"}>
                          <InputGroup.Text>$</InputGroup.Text>
                          <FormControl
                            value={this.state.globalState.totalUSDAirdropped}
                            onChange={(event) => {
                                console.log("event", event.target.value);
                                this.setState({
                                    globalState: {
                                        ...this.state.globalState,
                                        totalUSDAirdropped: event.target.value,
                                    }
                                })
                                console.log("state", this.state.globalState);
                            }}
                            placeholder={"Total USD Airdropped"}
                            aria-label="Total USD Airdropped"
                          />
                          <Button
                            variant="primary"
                            type="button"
                            onClick={() => this.updateGlobalState()}
                          >
                            update
                          </Button>
                        </InputGroup>
                      </div>

                      <Divider className={"my-2"} />

                      <Form.Label htmlFor="basic-url">
                        Total USD Raised
                      </Form.Label>
                      <div className={"d-flex flex-column"}>
                        <InputGroup className={"d-flex"}>
                          <InputGroup.Text>$</InputGroup.Text>
                          <FormControl
                            value={this.state.globalState.totalUSDRaised}
                            onChange={(event) =>
                                this.setState({
                                    globalState: {
                                      ...this.state.globalState,
                                      totalUSDRaised: event.target.value,
                                    }
                                })
                            }
                            placeholder={"Total USD Raised"}
                            aria-label="Total USD Raised"
                          />
                          <Button
                            variant="primary"
                            type="button"
                            onClick={() => this.updateGlobalState()}
                          >
                            update
                          </Button>
                        </InputGroup>
                      </div>
                    </Col>
                  </Row>
                </div>
              </Tab>
            </Tabs>
          </div>
        </div>
      </div>
    );
  }
}

SmartContractManagement.propTypes = {};

export default SmartContractManagement;
