/* eslint-disable jsx-a11y/anchor-is-valid */
import React from 'react';
import PropTypes from 'prop-types';
import BlockUi from 'react-block-ui';
class Table extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      ...this.readAndParseProps(props),
    };

    this.setUpEvents(props);
  }

  readAndParseProps(props) {
    let pageSize =
      this.state?.pagination?.pageSize ??
      props.initialPageSize ??
      props.tableConfig?.paginationOptions?.[0] ??
      10;
    let rowCount = props.rowCount ?? props.data?.length ?? 0;
    return {
      tableConfig: props.tableConfig ?? {},
      pagination: {
        pageSize: pageSize,
        rowCount: rowCount,
        page: !this.state?.pagination?.page
          ? props.startingPage ?? 1
          : this.state?.pagination?.page,
        pageCount: props?.pageCount ?? Math.ceil((rowCount ?? 1) / pageSize),
      },
      data: props.data ?? [],
    };
  }

  setUpEvents(props) {
    this.onPaginationChanges = props.onPaginationChanges
      ? () => props.onPaginationChanges(this.state.pagination)
      : () => {};
  }

  paginateTo(page, pageSize) {
    this.setState(
      {
        pagination: {
          ...this.state.pagination,
          page,
          pageSize,
        },
      },
      () => {
        this.onPaginationChanges();
      },
    );
  }

  componentDidUpdate(prevProps, prevState, snapshot) {
    if (
      prevProps.data?.length !== this.props.data?.length ||
      prevProps.tableConfig?.headers !== this.props?.tableConfig?.headers ||
      prevProps.tableConfig?.rowCount !== this.props?.tableConfig?.rowCount
    ) {
      this.setState({
        ...this.readAndParseProps(this.props),
      });
    }
  }

  render() {
    const { tableConfig, data, pagination } = this.state;
    const { blocking } = this.props;

    return (
      <div>
        <BlockUi tag='div' blocking={blocking}>
          <div className='table-responsive p-1'>
            <table
              className='table table-head-custom table-vertical-center'
              id='kt_advance_table_widget_1'>
              <thead>
                <tr className='text-left'>
                  {tableConfig.headers?.map((e, i) => {
                    return (
                      <th
                        key={'head' + i}
                        className={'pl-0 ' + e.classNames ?? ''}
                        style={e?.style ?? {}}>
                        {e.render ? e.render(tableConfig) : e.value}
                      </th>
                    );
                  })}
                </tr>
              </thead>
              <tbody>
                {this.props.children ??
                  data.map((dataCell, i) => {
                    return (
                      <tr key={'tb' + i}>
                        {tableConfig.headers?.map((e, i) => {
                          const value = e.cell ?? {};
                          return (
                            <td
                              key={'td' + e.id}
                              className={'pl-0 ' + (value?.className ?? '')}>
                              {value.value ??
                                (value.render
                                  ? value.render(dataCell[e.id], dataCell)
                                  : dataCell[e.id] ?? '')}
                            </td>
                          );
                        })}
                      </tr>
                    );
                  })}
              </tbody>
            </table>
            <div className='page-pagination-box d-flex justify-content-between align-items-center flex-wrap'>
              <div className='d-flex align-items-center py-3'>
                <select
                  defaultValue={'10'}
                  className='form-control form-control-sm font-weight-bold mr-4 border-0 bg-light'
                  onChange={(event) =>
                    this.paginateTo(pagination?.page - 1, event.target.value)
                  }
                  style={{ width: 75 }}>
                  {tableConfig?.paginationOptions ? (
                    tableConfig?.paginationOptions.map((e) => {
                      return (
                        <option key={'opt' + e} value={e}>
                          {e}
                        </option>
                      );
                    })
                  ) : (
                    <>
                      <option value='10'>10</option>
                      <option value='20'>20</option>
                      <option value='30'>30</option>
                      <option value='50'>50</option>
                      <option value='100'>100</option>
                    </>
                  )}
                </select>
                <a href='#' className='btn btn-icon btn-sm btn-light mr-2 my-1'>
                  <i className='ki ki-bold-double-arrow-next icon-xs'></i>
                </a>
                <span className='display-text'>
                  Displaying{' '}
                  {Math.min(pagination?.pageSize, pagination?.rowCount)} of{' '}
                  {pagination?.rowCount} records
                </span>
              </div>
              <div className='d-flex flex-wrap py-2 mr-3'>
                <button
                  disabled={pagination?.page <= 1}
                  onClick={() => this.paginateTo(1, pagination?.pageSize)}
                  className='btn btn-icon btn-sm btn-light mr-2 my-1'>
                  <i className='ki ki-bold-double-arrow-back icon-xs'></i>
                </button>
                <button
                  disabled={pagination?.page <= 1}
                  onClick={() =>
                    this.paginateTo(pagination?.page - 1, pagination?.pageSize)
                  }
                  className='btn btn-icon btn-sm btn-light mr-2 my-1'>
                  <i className='ki ki-bold-arrow-back icon-xs'></i>
                </button>

                {pagination?.page - 1 > 0 && (
                  <a
                    onClick={() =>
                      this.paginateTo(
                        pagination?.page - 1,
                        pagination?.pageSize,
                      )
                    }
                    className='btn btn-icon btn-sm border-0 btn-light mr-2 my-1'>
                    {' '}
                    {pagination?.page - 1}
                  </a>
                )}
                <button className='btn btn-icon btn-sm border-0 btn-light btn-hover-primary active mr-2 my-1'>
                  {pagination?.page}
                </button>
                {pagination?.page + 1 <= pagination?.pageCount && (
                  <a
                    onClick={() =>
                      this.paginateTo(
                        pagination?.page + 1,
                        pagination?.pageSize,
                      )
                    }
                    className='btn btn-icon btn-sm border-0 btn-light mr-2 my-1'>
                    {pagination?.page + 1}
                  </a>
                )}

                <button
                  disabled={pagination?.page >= pagination?.pageCount}
                  onClick={() =>
                    this.paginateTo(pagination?.page + 1, pagination?.pageSize)
                  }
                  className='btn btn-icon btn-sm btn-light mr-2 my-1'>
                  <i className='ki ki-bold-arrow-next icon-xs'></i>
                </button>
                <button
                  disabled={pagination?.page >= pagination?.pageCount}
                  onClick={() =>
                    this.paginateTo(pagination?.pageCount, pagination?.pageSize)
                  }
                  className='btn btn-icon btn-sm btn-light mr-2 my-1'>
                  <i className='ki ki-bold-double-arrow-next icon-xs'></i>
                </button>
              </div>
            </div>
          </div>
        </BlockUi>
      </div>
    );
  }
}

Table.propTypes = {
  tableConfig: PropTypes.shape({
    headers: PropTypes.array,
    paginationOptions: PropTypes.object,
    initialPageSize: PropTypes.number,
    rowCount: PropTypes.number,
    startingPage: PropTypes.number,
    pageCount: PropTypes.number,
    onPaginationChanges: PropTypes.func,
  }),
  data: PropTypes.array,
  style: PropTypes.object,
  blocking: PropTypes.bool,
};

export default Table;
