import React, {lazy, useEffect, Suspense} from "react";
import { Redirect, Switch, Route } from "react-router-dom";
import {LayoutSplashScreen, ContentRoute} from "../_metronic/layout";
import { BuilderPage } from "./pages/BuilderPage";
import { MyPage } from "./pages/MyPage";
import { DashboardPage } from "./pages/DashboardPage";
import store from "../redux/store";
import {getStaticVars} from "../redux/actions/utilsActions";
import AirdropsPage from "./pages/listings/AirdropsPage";
import LaunchpadsPage from "./pages/listings/LaunchpadsPage";
import TokesPage from "./pages/listings/TokesPage";
import WalletsPage from "./pages/listings/WalletsPage";
import smartContractManagement from "./pages/smartContractManagement";

const GoogleMaterialPage = lazy(() =>
  import("./modules/GoogleMaterialExamples/GoogleMaterialPage")
);
const ReactBootstrapPage = lazy(() =>
  import("./modules/ReactBootstrapExamples/ReactBootstrapPage")
);
const ECommercePage = lazy(() =>
  import("./modules/ECommerce/pages/eCommercePage")
);
const UserProfilepage = lazy(() =>
  import("./modules/UserProfile/UserProfilePage")
);

const TreesPage = lazy(() =>
    import("../app/pages/trees")
);

export default function BasePage() {
  // useEffect(() => {
  //   console.log('Base page');
  // }, []) // [] - is required if you need only one call
  // https://reactjs.org/docs/hooks-reference.html#useeffect

  useEffect(() => {
    store.dispatch(getStaticVars())
  });

  return (
    <Suspense fallback={<LayoutSplashScreen />}>
      <Switch>
        {
          /* Redirect from root URL to /dashboard. */
          <Redirect exact from="/" to="/airdrops" />
        }
        <ContentRoute path="/dashboard" component={DashboardPage} />
        <ContentRoute path="/builder" component={BuilderPage} />
        <ContentRoute path="/my-page" component={MyPage} />
        <Route path="/google-material" component={GoogleMaterialPage} />
        <Route path="/react-bootstrap" component={ReactBootstrapPage} />
        <Route path="/e-commerce" component={ECommercePage} />
        <Route path="/user-profile" component={UserProfilepage} />
        <ContentRoute from="/airdrops" component={AirdropsPage} />
        <ContentRoute from="/launchpads" component={LaunchpadsPage} />
        <ContentRoute from="/tokens" component={TokesPage} />
        <ContentRoute from="/wallets" component={WalletsPage} />
        {/* <Route path="/globalVars" component={GlobalVars} /> */}
        <Route path="/trees" component={TreesPage} />
        <Route path="/contracts" component={smartContractManagement} />
        <Redirect to="error/error-v1" />
      </Switch>
    </Suspense>
  );
}
