/**
 * The Error Handler Class : meant to be used with the Requests to Bridge API
 */
export default class errorHandler {

  constructor(promise) {
    promise.catch(
      err => {
        //handle error,

        //must throw in order to let the user handle their error,
        console.log(err);
        throw err;
      }
    );

    return promise;
  }
}



