import {forger} from '../RequestForgerer'
import URLS from './urls';


class Wallets {

    getWallet({address, id}) {
        // prepare url
        let url = address ? URLS.GET_WALLET_BY_ADDRESS + address : URLS.GET_WALLET_BY_ID + id;
        console.log(url);
        // forge the url
        return forger.forge({
            method:'GET',
            url:url,
        });
    };

    createAWallet(name, address, info) {
        let url = URLS.SAVE_NEW_WALLET;
        console.log(url);
        // forge the url
        return forger.forge({
            method:'POST',
            url:url,
            payload: {
                name,
                address,
                info
            }
        });
    }

    getEnrolledAirdrops(addr, signature){
        let url = URLS.ENROLLED_AIRDROPS;
        console.log(url);
        // forge the url
        return forger.forge({
            method:'POST',
            url:url,
            payload:{
                address: addr,
                signature: signature
            }
        });
    }

    didAddressWin(addr, airdropID){
        let url = URLS.DID_ADDRESS_WIN_AIRDROP;
        console.log(url);
        // forge the url
        return forger.forge({
            method:'POST',
            url:url,
            payload:{
                address: addr,
                airdropId: airdropID
            }
        });
    }


    getWalletsWithProfiles({limit, offset}){
        let url = URLS.GET_ALL_WALLETS_WITH_PROFILES;
        console.log(url);
        // forge the url
        return forger.forge({
            method:'GET',
            url:url,
            payload:{
                limit, offset
            }
        });
    }

    deleteProfile(address){
        let url = URLS.DELETE_PROFILE + '/' + address
        console.log(url);
        // forge the url
        return forger.forge({
            method:'DELETE',
            url:url,
        });
    }



}


export default Wallets;
