import {GET_STATIC_VARS, GET_STATIC_VARS_ERROR, SWITCH_SIDEBAR} from '../types/utilTypes'



const INITIAL_STATE = {
    staticVars:{
        airdrop_apply_now_link: "https://docs.google.com/forms/d/e/1FAIpQLScAwbd6qhB--qtgAdsjtvmorNtPBdJ15kyYoAMr5QbbwqjzgQ/viewform"
    },
    view:{
      sidebar_active: false
    },
    error:null,
}

export const Utils = (state = INITIAL_STATE, action) => {
    switch (action.type) {
        case GET_STATIC_VARS: {
            return {
                ...state,
                staticVars: {...state.staticVars, ...action.payload},
            };
        }
        case GET_STATIC_VARS_ERROR: {
            return {
                ...state,
                error: action.payload,
            };
        }
        case SWITCH_SIDEBAR: {
            return {
                ...state,
                view: {
                    ...state.view,
                    sidebar_active: !state.view?.sidebar_active
                }
            };
        }
        default:
            return state;
    }
}

export default Utils
