import {forger} from '../RequestForgerer'
import URLS from './urls';


class AirDrops {

    getAirdrops(address, {page =10, pageSize =1}) {
        // prepare url
        let url = address ? URLS.GET_ALL_AIRDROPS+"?address="+address +"&page=" + page + "&pageSize=" + pageSize : URLS.GET_ALL_AIRDROPS +"?page=" + page + "&pageSize=" + pageSize;
        console.log(url);
        // forge the url
        return forger.forge({
            method:'GET',
            url:url,
        });
    };


    createAirdrop(payload){
        // prepare url
        let url = URLS.GET_ALL_AIRDROPS;
        console.log(url);
        // forge the url
        return forger.forge({
            method:'POST',
            url: URLS.BACKEND_URL + "/process",
            payload: {
                url,
                params: payload,
            }
        });
    }

    updateAirdrop(payload){
        // prepare url
        let url = URLS.GET_ALL_AIRDROPS;
        console.log(url);
        // forge the url
        return forger.forge({
            method:'PUT',
            url:url,
            payload: payload
        });
    }

    getSingleAirdrop(id){
        // prepare url
        let url = URLS.GET_SINGLE_AIRDROP;
        console.log(url);
        // forge the url
        return forger.forge({
            method:'GET',
            url:url+id,
        });
    }


    enterAirdrop(address, airdropId, name){
        let url = URLS.ENROLL_INTO_AIRDROP;
        console.log(url);
        // forge the url
        return forger.forge({
            method:'POST',
            url:url,
            payload:{
                name,
                walletAddress: address,
                airdropId
            }
        });
    }

    finishAirdrop(airdropId){
        let url = URLS.FINISH_AIRDROP;
        console.log(url);
        // forge the url
        return forger.forge({
            method:'POST',
            url:url,
            payload:{
                airdropId,
            }
        });
    }


    getAirdropSubmissions(airdropId, {limit, offset}){
        let url = URLS.GET_AIRDROP_SUBMISSIONS;
        console.log(url);
        // forge the url
        return forger.forge({
            method:'GET',
            url:url,
            payload:{
                airdropId,
                limit, offset
            }
        });
    }

}


export default AirDrops;
